<template>
    <v-data-table :headers="headers" :items="items" sort-by="daoqishijian" class="elevation-2" style="flex:1">
        <template v-slot:top>
            <v-snackbar app top timeout="2000" :color="snackbar.color" v-model="snackbar.show">{{ snackbar.text }}
            </v-snackbar>
            <v-toolbar flat>
                <v-toolbar-title>企业列表</v-toolbar-title>
                <v-spacer></v-spacer>
                <v-dialog v-model="dialog" max-width="500px">
                    <template v-slot:activator="{ on, attrs }">
                        <v-btn color="primary" class="mb-2" v-bind="attrs" v-on="on">
                            新增
                        </v-btn>
                    </template>
                    <v-card>
                        <v-card-title>
                            <span class="text-h5">{{ formTitle }}</span>
                        </v-card-title>

                        <v-card-text>
                            <v-container>
                                <v-row>
                                    <v-col cols="12" sm="12" md="12">
                                        <v-text-field v-model="editedItem.qiyequancheng" label="企业全称"></v-text-field>
                                    </v-col>
                                    <v-col cols="12" sm="6" md="6">
                                        <v-text-field v-model="editedItem.qiyejiancheng" label="企业简称"></v-text-field>
                                    </v-col>
                                    <v-col cols="12" sm="6" md="6">
                                        <v-dialog v-model="date_picker_dialog" width="290px">
                                            <template v-slot:activator="{ on }">
                                                <v-text-field readonly v-model="editedItem.daoqishijian" label="到期时间"
                                                    v-on="on"></v-text-field>
                                            </template>
                                            <v-date-picker v-model="editedItem.daoqishijian"
                                                @input="date_picker_dialog = false" locale="zh-cn"></v-date-picker>
                                        </v-dialog>
                                    </v-col>
                                    <v-col cols="12" sm="6" md="6">
                                        <v-text-field v-model="editedItem.mp_appid" label="公众号appid"></v-text-field>
                                    </v-col>
                                    <v-col cols="12" sm="6" md="6">
                                        <v-text-field v-model="editedItem.mini_appid" label="小程序appid"></v-text-field>
                                    </v-col>
                                    <v-col cols="12" sm="12" md="12">
                                        <v-text-field v-model="editedItem.mchid" label="商户号"></v-text-field>
                                    </v-col>
                                    <v-col cols="12" sm="6" md="6">
                                        <v-text-field v-model="editedItem.lianxiren" label="联系人"></v-text-field>
                                    </v-col>
                                    <v-col cols="12" sm="6" md="6">
                                        <v-text-field v-model="editedItem.lianxidianhua" label="联系电话"></v-text-field>
                                    </v-col>
                                </v-row>
                            </v-container>
                        </v-card-text>

                        <v-card-actions>
                            <v-spacer></v-spacer>
                            <v-btn color="blue darken-1" text @click="close">
                                取消
                            </v-btn>
                            <v-btn color="blue darken-1" text @click="save">
                                保存
                            </v-btn>
                        </v-card-actions>
                    </v-card>
                </v-dialog>
            </v-toolbar>
        </template>
        <template v-slot:[`item.mchid`]="{ item }">
            <span v-if="item.mchid" class="green--text">{{ item.mchid }}</span>
        </template>
        <template v-slot:[`item.actions`]="{ item }">
            <v-icon color="primary" small @click="editItem(item)">
                mdi-pencil
            </v-icon>
        </template>
    </v-data-table>
</template>
<script>
export default {
    data: () => ({
        snackbar: {
            show: false,
            text: "",
            color: "success",
        },
        dialog: false,
        date_picker_dialog: false,
        headers: [
            { text: 'id', sortable: false, value: '_id', },
            { text: '企业全称', sortable: false, value: 'qiyequancheng', },
            { text: '企业简称', sortable: false, value: 'qiyejiancheng' },
            { text: '公众号appid', sortable: false, value: 'mp_appid' },
            { text: '小程序appid', sortable: false, value: 'mini_appid' },
            { text: '商户号mchid', sortable: false, value: 'mchid' },
            { text: '联系人', sortable: false, value: 'lianxiren' },
            { text: '联系电话', sortable: false, value: 'lianxidianhua' },
            { text: '到期时间', value: 'daoqishijian' },
            { text: '编辑/删除', align: 'center', sortable: false, value: 'actions' },
        ],
        items: [],
        editedIndex: -1,
        editedItem: {
            qiyequancheng: '',
            qiyejiancheng: '',
            mp_appid: '',
            mini_appid: '',
            mchid: '',
            lianxiren: '',
            lianxidianhua: '',
            daoqishijian: (new Date()).toISOString().substr(0, 10)
        },
        defaultItem: {
            qiyequancheng: '',
            qiyejiancheng: '',
            mp_appid: '',
            mini_appid: '',
            mchid: '',
            lianxiren: '',
            lianxidianhua: '',
            daoqishijian: (new Date()).toISOString().substr(0, 10)
        },
    }),

    computed: {
        formTitle() {
            return this.editedIndex === -1 ? '添加' : '编辑'
        },
    },

    watch: {
        dialog(val) {
            val || this.close()
        }
    },

    created() {
        this.getlist()
    },

    methods: {
        getlist() {
            this.$axios.get('/company/getlist')
                .then((res) => {
                    // 格式化时间
                    res.data.forEach((item) => {
                        item.daoqishijian = item.daoqishijian.substr(0, 10)
                    })
                    this.items = res.data
                })
        },

        editItem(item) {
            this.editedIndex = this.items.indexOf(item)
            this.editedItem = Object.assign({}, item)
            this.dialog = true
        },

        close() {
            this.dialog = false
            this.$nextTick(() => {
                this.editedItem = Object.assign({}, this.defaultItem)
                this.editedIndex = -1
            })
        },

        save() {
            if (this.editedIndex > -1) {
                // 更新
                this.$axios.post('/company/update', this.editedItem)
                    .then((res) => {
                        console.log(res)
                        this.close()
                        this.getlist()
                        this.snackbar = {
                            show: true,
                            text: res.data.message,
                            color: res.data.errors ? "error" : "success",
                        }
                    })
                    .catch(error => {
                        console.log(error)
                        this.snackbar = {
                            show: true,
                            text: error,
                            color: "error"
                        }
                    })
            } else {
                // 添加
                this.$axios.post('/company/create', this.editedItem)
                    .then((res) => {
                        console.log(res)
                        this.close()
                        this.getlist()
                        this.snackbar = {
                            show: true,
                            text: res.data.message,
                            color: res.data.errors ? "error" : "success",
                        }
                    })
                    .catch(error => {
                        console.log(error)
                        this.snackbar = {
                            show: true,
                            text: error,
                            color: "error"
                        }
                    })
            }
        },
    },
}
</script>