<template>
    <v-data-table :headers="headers" :items="items" item-key="_id" class="elevation-2" style="flex:1">
        <template v-slot:top>
            <v-snackbar app top timeout="2000" :color="snackbar.color" v-model="snackbar.show">{{ snackbar.text }}
            </v-snackbar>
            <v-toolbar flat>
                <v-toolbar-title>账号列表</v-toolbar-title>
                <v-btn-toggle v-model="filter_status" class="mx-5" rounded dense mandatory>
                    <v-btn small>
                        可用
                    </v-btn>
                    <v-btn small>
                        禁用
                    </v-btn>
                </v-btn-toggle>
                <v-spacer></v-spacer>
                <v-dialog v-model="dialog" max-width="500px">
                    <template v-slot:activator="{ on, attrs }">
                        <v-btn color="primary" class="mb-2" v-bind="attrs" v-on="on">
                            注册
                        </v-btn>
                    </template>
                    <v-card>
                        <v-card-title>
                            <span class="text-h5">{{ formTitle }}</span>
                        </v-card-title>

                        <v-card-text>
                            <v-container>
                                <v-row>
                                    <v-col cols="12" sm="6" md="6">
                                        <v-text-field v-model="editedItem.username" label="账号"></v-text-field>
                                    </v-col>
                                    <v-col cols="12" sm="6" md="6">
                                        <v-text-field v-model="editedItem.password" label="密码"></v-text-field>
                                    </v-col>
                                    <v-col cols="12" sm="6" md="6">
                                        <v-text-field v-model="editedItem.nickname" label="姓名"></v-text-field>
                                    </v-col>
                                    <v-col cols="12" sm="6" md="6">
                                        <v-select :items="company_items" item-text="qiyejiancheng" item-value="_id"
                                            v-model="editedItem.company" label="企业"></v-select>
                                    </v-col>
                                    <v-col cols="12" sm="6" md="6">
                                        <v-select :items="permission_items" item-text="name" item-value="_id"
                                            v-model="editedItem.permission" label="权限"></v-select>
                                    </v-col>
                                </v-row>
                            </v-container>
                        </v-card-text>

                        <v-card-actions>
                            <v-spacer></v-spacer>
                            <v-btn color="blue darken-1" text @click="close">
                                取消
                            </v-btn>
                            <v-btn color="blue darken-1" text @click="save">
                                保存
                            </v-btn>
                        </v-card-actions>
                    </v-card>
                </v-dialog>
            </v-toolbar>
        </template>
        <template v-slot:[`item.status`]="{ item }">
            <v-switch @change="change_status(item)" v-model="item.status"></v-switch>
        </template>
        <template v-slot:[`item.actions`]="{ item }">
            <v-icon color="primary" small class="mr-2" @click="editItem(item)">
                mdi-pencil
            </v-icon>
            <v-dialog :v-model="false" max-width="300px">
                <template v-slot:activator="{ on, attrs }">
                    <v-icon color="green" small class="mr-2" @click="bind_wechat_user(item)" v-bind="attrs" v-on="on">
                        mdi-wechat
                    </v-icon>
                </template>
                <v-card>
                    <v-card-title>微信扫码</v-card-title>
                    <v-card-text class="text-center">
                        <img :src="qrcode_img_src">
                        <p>{{qrcode_url}}</p>
                    </v-card-text>
                </v-card>
            </v-dialog>
        </template>
    </v-data-table>
</template>
<script>
import QRCode from "qrcode";
export default {
    data: () => ({
        snackbar: {
            show: false,
            text: "",
            color: "success",
            site_error: ''
        },
        filter_status: 0,
        dialog: false,
        company_items: [],
        permission_items: [],
        items: [],
        editedIndex: -1,
        editedItem: {
            username: '',
            password: '',
            nickname: '',
            company: '',
            permission: '',
            status: true
        },
        defaultItem: {
            username: '',
            password: '',
            nickname: '',
            company: '',
            permission: '',
            status: true
        },
        qrcode_url:'',
        qrcode_img_src:''
    }),

    computed: {
        formTitle() {
            return this.editedIndex === -1 ? 'New Item' : 'Edit Item'
        },
        headers() {
            return [
                { text: 'id', sortable: false, value: '_id', },
                { text: '账号', sortable: false, value: 'username', },
                { text: '密码', sortable: false, value: 'password' },
                { text: '姓名', sortable: false, value: 'nickname' },
                { text: '企业', sortable: false, value: 'company.qiyejiancheng' },
                { text: '权限', sortable: false, value: 'permission.name' },
                {
                    text: '状态', sortable: false, value: 'status', filter: (value) => {
                        return this.filter_status?!value:value
                    },
                },
                { text: '绑定微信', sortable: false, value: 'wexinuser' },
                { text: '编辑/绑定', align: 'center', sortable: false, value: 'actions' },
            ]
        },
    },

    watch: {
        dialog(val) {
            val || this.close()
        }
    },

    created() {
        this.getlist()
        // 获取企业列表
        this.$axios.get('/company/getlist')
            .then((res) => {
                this.company_items = res.data
            })
        // 获取权限列表
        this.$axios.get('/permission')
            .then((res) => {
                this.permission_items = res.data
            })
    },

    methods: {
        getlist() {
            this.$axios.get('/user/lists')
                .then((res) => {
                    this.items = res.data
                })
        },
        editItem(item) {
            this.editedIndex = this.items.indexOf(item)
            this.editedItem = Object.assign({}, item)
            this.dialog = true
        },
        close() {
            this.dialog = false
            this.$nextTick(() => {
                this.editedItem = Object.assign({}, this.defaultItem)
                this.editedIndex = -1
            })
        },
        change_status(item) {
            // 更新状态
            this.$axios.post('/user/update', item)
                .then((res) => {
                    this.snackbar = {
                        show: true,
                        text: res.data.message,
                        color: res.data.errors ? "error" : "success",
                    }
                })
                .catch(error => {
                    console.log(error)
                    this.snackbar = {
                        show: true,
                        text: error,
                        color: "error"
                    }
                })
        },
        save() {
            if (this.editedIndex > -1) {
                // 更新
                this.$axios.post('/user/update', this.editedItem)
                    .then((res) => {
                        console.log(res)
                        this.close()
                        this.getlist()
                        this.snackbar = {
                            show: true,
                            text: res.data.message,
                            color: res.data.errors ? "error" : "success",
                        }
                    })
                    .catch(error => {
                        console.log(error)
                        this.snackbar = {
                            show: true,
                            text: error,
                            color: "error"
                        }
                    })
            } else {
                // 添加
                this.$axios.post('/user/add', this.editedItem)
                    .then((res) => {
                        console.log(res)
                        this.close()
                        this.getlist()
                        this.snackbar = {
                            show: true,
                            text: res.data.message,
                            color: res.data.errors ? "error" : "success",
                        }
                    })
                    .catch(error => {
                        console.log(error)
                        this.snackbar = {
                            show: true,
                            text: error,
                            color: "error"
                        }
                    })
            }
        },

        // 绑定微信用户
        bind_wechat_user(item) {
            this.qrcode_img_src = ''
            this.qrcode_url = ''
            
            // 获取企业绑定的小程序appid
            if (!item.company) {
                this.qrcode_url = '该账号未绑定企业，无法绑定用户，请联系管理员添加'
                return
            }
            if (!item.company.mini_appid || item.company.mini_appid == '') {
                this.qrcode_url = '该企业未绑定小程序，无法绑定用户，请联系管理员添加'
                return
            } else {
                var mini_appid = item.company.mini_appid
                let _this = this

                this.qrcode_url = 'https://api.dayangtong.cn:8080/' + mini_appid + '?pc_user_id=' + item._id
                // 生成普通二维码，微信扫码打开小程序
                QRCode.toDataURL(this.qrcode_url, function (err, url) {
                    _this.qrcode_img_src = url
                    
                })
            }
        }
    },
}
</script>