<template>
    <v-row>
        <v-snackbar app top timeout="2000" :color="snackbar.color" v-model="snackbar.show">{{ snackbar.text }}
        </v-snackbar>
        <v-overlay :value="overlay">
            <v-progress-circular indeterminate></v-progress-circular>
        </v-overlay>
        <v-col cols="12" sm="3" md="3" style="height:100%">
            <v-card style="height:100%" class="d-flex flex-column">
                <v-card-title>房屋列表</v-card-title>
                <v-card-text class="d-flex flex-column" style="height:100%;overflow:auto;">
                    <v-select :items="xiaoqu_items" item-text="name" item-value="_id" label="选择小区" v-model="xiaoqu"
                        @change="change_xiaoqu"></v-select>
                    <!-- 判断小区对象是否为空 -->
                    <v-alert type="error" outlined dense v-show="loudong_items.length == 0">您还没有选择小区！
                    </v-alert>
                    <v-treeview style="height:100%;overflow:auto" :items="loudong_items" item-key="_id" transition
                        :load-children="fetchFangwus" :active.sync="active" return-object open-on-click dense
                        activatable>
                        <template v-slot:prepend="{ item }">
                            <v-icon v-if="item.xiaoqu" small>
                                mdi-city
                            </v-icon>
                            <v-icon v-if="item.loudong" small>
                                mdi-door-open
                            </v-icon>
                            <v-icon v-if="item.danyuan" small>
                                mdi-home
                            </v-icon>
                        </template>
                        <template v-slot:label="{ item }">
                            <span v-if="item.danyuan">{{ item.name }}【{{ item.yezhu }}】<v-icon
                                    v-if="item.wx_user.length > 0" color="success" small>mdi-wechat</v-icon></span>
                            <span v-else>{{ item.name }}</span>
                        </template>
                    </v-treeview>
                </v-card-text>
            </v-card>
        </v-col>
        <v-col cols="12" sm="9" md="9" style="height:100%">
            <v-card style="height:100%" class="d-flex flex-column">
                <v-card-title>
                    {{ active.length ? active[0].name : null }}费用列表
                    <!-- 换表 -->
                    <v-btn class="ml-5" color="warning"
                        :disabled="!(selected_feiyong.length == 1 && selected_feiyong[0].feixiang.leixing == '按读表')"
                        small @click="btn_change_biao">换 表
                    </v-btn>
                    <!-- 删除选择费用 -->
                    <v-btn class="ml-5" color="error" :disabled="selected_feiyong.length == 0" small @click="btn_del">
                        删 除
                    </v-btn>
                    <!-- <v-divider vertical></v-divider> -->
                    <v-spacer></v-spacer>
                    <!-- 催款通知书 -->
                    <v-dialog v-model="cuikuan_dialog" max-width="900px">
                        <template v-slot:activator="{ on, attrs }">
                            <v-btn v-show="false" class="ml-5" :disabled="selected_feiyong.length == 0" v-bind="attrs"
                                v-on="on" color="secondary" small>催款书
                            </v-btn>
                        </template>
                        <v-card>
                            <v-card-title>
                                <v-spacer></v-spacer>
                                <v-btn color="blue darken-1" text @click="cuikuan_dialog = false">
                                    取消
                                </v-btn>
                                <v-btn color="blue darken-1" text @click="print">
                                    打印
                                </v-btn>
                            </v-card-title>
                            <v-card-text>
                                <!-- lodop错误提示信息 -->
                                <div ref="lodop_msg"></div>
                                <div ref="print">
                                    <div style="padding:10px;color:#222;font-size: 16px;">
                                        <div
                                            style="text-align:center;font-size: 24px;font-weight: bold;line-height: 2;">
                                            催款通知书</div>
                                        <div>
                                            <p>尊敬的业主您好：</p>
                                            <p>衷心感谢广大业主/住户对小区物业服务工作的理解、信任和支持！</p>
                                            <p>按时交纳物业费是每位业主的义务，也是物业公司维持日常运营的基本保障。物业费是小区公共秩序维护、环境卫生、公共区域维修，以及电梯、配电、公共照明等设备设施正常运转的根本保障。如您不能按时足额交纳物业费，将会对小区物业管理服务工作造成严重的影响。同时，也侵犯了已交费业主的切身利益。
                                            </p>
                                            <p>若有不明，请致电物业客服中心咨询。咨询电话：{{cuikuan_info.xiaoqu_phone}}</p>
                                            <p>{{(new Date()).toLocaleDateString()}}</p>
                                            <div style="text-align:center">
                                                <img :src="cuikuan_info.mp_qr_code">
                                            </div>
                                        </div>
                                    </div>
                                </div>
                            </v-card-text>
                        </v-card>
                    </v-dialog>
                    <!-- 刷新 -->
                    <v-btn class="ml-5" :disabled="!active.length" small color="accent" @click="refresh">刷 新
                    </v-btn>
                    <!-- 新增费用 -->
                    <v-dialog v-model="dialog" max-width="500px">
                        <template v-slot:activator="{ on, attrs }">
                            <v-btn class="ml-5" color="info" v-bind="attrs" v-on="on" :disabled="!active.length" small>
                                新 增
                            </v-btn>
                        </template>
                        <v-card>
                            <v-card-title>
                                {{ formTitle }}
                            </v-card-title>

                            <v-card-text>
                                <div class="d-flex flex-wrap">
                                    <v-checkbox v-for="item in feixiang_items" :key="item._id" :value="item"
                                        :disabled="!item.status" v-model="check_feixiang" :label="item.mingcheng"
                                        class="mr-8">
                                    </v-checkbox>
                                </div>
                            </v-card-text>

                            <v-card-actions>
                                <v-spacer></v-spacer>
                                <v-btn color="blue darken-1" text @click="dialog = false">
                                    取消
                                </v-btn>
                                <v-btn color="blue darken-1" text @click="create">
                                    添加
                                </v-btn>
                            </v-card-actions>
                        </v-card>
                    </v-dialog>
                </v-card-title>
                <v-card-text style="flex:1;height:0" class="d-flex flex-column">
                    <v-data-table v-model="selected_feiyong" :headers="headers" :items="feiyong_items" item-key="_id"
                        :loading="loading_feiyong" show-select hide-default-footer disable-pagination height="38vh"
                        checkbox-color="primary">
                        <!-- 是否欠费字段 -->
                        <template v-slot:[`item.qianfei`]="{ item }">
                            <span style="color:red">{{ item.qianfei }}</span>
                        </template>

                        <!-- 起数字段 -->
                        <template v-slot:[`item.start`]="{ item }">
                            <v-edit-dialog v-if="item.feixiang.leixing == '按读表'" :return-value.sync="item.start"
                                @save="save_start(item)" @cancel="cancel" @open="open" @close="close" large
                                cancel-text="取消" save-text="更新">
                                <span class="blue--text">{{ item.start }}</span>
                                <template v-slot:input>
                                    <v-text-field v-model="item.start" type="number"></v-text-field>
                                </template>
                            </v-edit-dialog>
                            <span v-else-if="item.feixiang.leixing == '临时费'">{{ item.start }}</span>
                            <!-- 如果是按面积||按住户||暖气费 -->
                            <v-edit-dialog v-else :return-value.sync="item.start" @save="save_start(item)"
                                @cancel="cancel" @open="open" @close="close" large cancel-text="取消" save-text="更新">
                                <span class="blue--text">{{ item.start }}</span>
                                <template v-slot:input>
                                    <v-date-picker no-title v-model="item.start" scrollable locale="zh-cn">
                                    </v-date-picker>
                                </template>
                            </v-edit-dialog>
                        </template>
                        <!-- 止数字段 -->
                        <template v-slot:[`item.end`]="{ item }">
                            <v-edit-dialog v-if="item.feixiang.leixing == '按读表'" :return-value.sync="item.end"
                                @save="save_end(item)" @cancel="cancel" @open="open" @close="close" large
                                cancel-text="取消" save-text="更新">
                                <span class="blue--text">{{ item.end }} | {{ item.change_time }}</span>
                                <template v-slot:input>
                                    <v-text-field v-model="item.end" type="number"></v-text-field>
                                </template>
                            </v-edit-dialog>
                            <span v-else>{{ item.end }}</span>
                        </template>
                        <!-- 周期/倍数字段 -->
                        <template v-slot:[`item.zhouqi_beishu`]="{ item }">
                            <v-edit-dialog :return-value="item.zhouqi_beishu" @save="save_zhouqi_beishu(item)"
                                @cancel="cancel" @open="open" @close="close" large cancel-text="取消" save-text="更新">
                                <span class="blue--text">{{ item.zhouqi_beishu }}</span>
                                <template v-slot:input>
                                    <v-text-field
                                        v-if="item.feixiang.leixing == '按面积' || item.feixiang.leixing == '按户数' || item.feixiang.leixing == '取暖费'"
                                        v-model="item.zhouqi_beishu" type="number">
                                    </v-text-field>
                                    <v-text-field v-else v-model="item.zhouqi_beishu" type="number"></v-text-field>
                                </template>
                            </v-edit-dialog>
                        </template>
                        <!-- 数量字段 -->
                        <template v-slot:[`item.shuliang`]="{ item }">
                            <span v-if="item.feixiang.leixing == '临时费' || item.feixiang.leixing == '按户数'">
                                <v-edit-dialog :return-value.sync="item.shuliang" @save="save(item)" @cancel="cancel"
                                    @open="open" @close="close" large cancel-text="取消" save-text="更新">
                                    <span class="blue--text">{{ item.shuliang }}</span>
                                    <template v-slot:input>
                                        <v-text-field v-model="item.shuliang" type="number"></v-text-field>
                                    </template>
                                </v-edit-dialog>
                            </span>
                            <span v-else>{{ item.shuliang }}</span>
                        </template>
                        <!-- 总计字段 -->
                        <template v-slot:[`item.zongji`]="{ item }">
                            {{ Number(item.zongji).toFixed(2) }}
                        </template>
                    </v-data-table>
                    <div class="mt-2 d-flex justify-space-between" style="flex:1;height:0">
                        <div class="mr-5 pa-5 grey lighten-5 rounded caption text-no-wrap logs">
                            <v-progress-circular indeterminate color="primary" v-show="loading_history">
                            </v-progress-circular>
                            <div v-for="item in history_items" :key='item._id'>
                                {{ item.date }}@
                                <span v-if="item.user">{{ item.user.nickname }}
                                    <span v-if="!item.user.status" class="red--text">【离职】</span>
                                </span>
                                <span v-else>微信小程序</span>
                                | {{ item.info }}
                                <v-btn fab text x-small :color="item.shoukuandan.status ? 'error' : 'success'"
                                    v-if="item.shoukuandan"
                                    @click="shoukuandan_id = item.shoukuandan._id; shoukuandan_dialog = true">
                                    查
                                </v-btn>
                                <v-divider class="mb-1"></v-divider>
                            </div>
                        </div>
                        <div style="flex:1;overflow: auto;" class="d-flex flex-column">
                            <div class="pa-4 d-flex justify-space-around blue lighten-5 rounded">
                                <span class="text-button">余额：{{ active.length ? Number(active[0].yu_e).toFixed(2) :
                                0.00
                                }}</span>
                                <v-text-field v-model="text_field_yu_e" type="number" class="pa-0 mx-10"
                                    placeholder="负数为支取，正数为预存" :disabled="!active.length" dense hide-details>
                                </v-text-field>
                                <v-btn small :disabled="!active.length" @click="btn_modify_yu_e">确定</v-btn>
                            </div>
                            <div>
                                <v-radio-group v-model="radio_pay_way" row label="收款方式："
                                    :disabled="selected_feiyong.length == 0 ? true : false">
                                    <v-radio label="现金" value="现金" color="error"></v-radio>
                                    <v-radio label="微信" value="微信" color="success"></v-radio>
                                    <v-radio label="支付宝" value="支付宝" color="info"></v-radio>
                                    <v-radio label="银行卡" value="银行卡" color="warning"></v-radio>
                                    <v-radio label="其他" value="其他" color="gray"></v-radio>
                                </v-radio-group>
                            </div>
                            <v-textarea label="备注" v-model="remark" no-resize rows="2"></v-textarea>
                            <v-text-field label="优惠" v-model="youhui" type="number"></v-text-field>
                        </div>
                    </div>
                </v-card-text>
                <v-card-actions class="blue lighten-5">
                    <span>【房屋名称：{{ active.length ? active[0].name : null }}】【面积：{{ active.length ? active[0].mianji :
                    null
                    }}】【业主姓名：{{ active.length ? active[0].yezhu : null }}】</span>
                    <v-tooltip top>
                        <template v-slot:activator="{ on, attrs }">
                            <v-btn class="ml-5" :disabled="!(active.length && active[0].wx_user.length)" color="success"
                                @click="push" :loading="loading_shouqu" v-bind="attrs" v-on="on">推送缴费通知
                            </v-btn>
                        </template>
                        <div class="pa-2">
                            禁止重复推送、频繁推送<br>
                            1、业主小程序端只会展示欠费项目<br>
                            2、在备注中可添加欠费的备注信息<br>
                            3、推送时不允许添加优惠金额(无效)
                        </div>
                    </v-tooltip>
                    <v-spacer></v-spacer>
                    <span class="mr-5">总计:{{ total.toFixed(2) }}
                        <span v-if="youhui && youhui != '0'">-优惠:{{ Number(youhui).toFixed(2) }}</span>
                        = <span class="text-h5 font-weight-bold red--text">￥{{ (total - (youhui ? youhui :
                        0)).toFixed(2)
                        }}</span>
                    </span>
                    <v-btn x-large color="success" :disabled="selected_feiyong.length == 0 ? true : false"
                        @click="btn_ok" :loading="loading_shouqu">确认收取</v-btn>
                </v-card-actions>
                <v-dialog v-model="shoukuandan_dialog" width="900">
                    <shoukuandan v-on:close="shoukuandan_dialog = false;refresh()" :shoukuandan_id="shoukuandan_id">
                    </shoukuandan>
                </v-dialog>
            </v-card>
        </v-col>
    </v-row>
</template>
<script>
import shoukuandanComponent from './components/shoukuandanView.vue'
import { getLodop } from '@/utils/LodopFuncs' //导入模块
export default {
    data: () => ({
        snackbar: {
            show: false,
            text: "",
            color: "success",
        },
        overlay: false,
        loading_feiyong: false,
        loading_history: false,
        loading_shouqu: false,
        // 活动节点
        active: [],

        // 房屋历史记录
        history_items: [],
        // 被选择的费项
        check_feixiang: [],

        dialog: false,
        cuikuan_dialog: false,
        cuikuan_info: {
            xiaoqu_phone: '18306817521',
            mp_qr_code: 'https://api.dayangtong.cn:8080/uploads/mp_qrcode_img/qr_code_img-1661525036592.png'
        },
        shoukuandan_dialog: false,
        shoukuandan_id: '',

        // 预存金额
        text_field_yu_e: '',

        // 优惠金额
        youhui: '',
        formTitle: '添加',
        // 公司及小区数组
        xiaoqu_items: [],
        loudong_items: [],
        feixiang_items: [],

        // 费用列表
        headers: [
            { text: "状态", sortable: false, value: "qianfei" },
            { text: "类型", sortable: false, value: "feixiang.leixing" },
            { text: "名称", sortable: true, value: "mingcheng" },
            { text: "单价", sortable: false, value: "danjia" },
            { text: "起数", sortable: false, value: "start" },
            { text: "止数", sortable: false, value: "end" },
            { text: "周期/倍数", sortable: false, value: "zhouqi_beishu" },
            { text: "数量", sortable: false, value: "shuliang" },
            { text: "总计", sortable: false, value: "zongji" },
        ],
        feiyong_items: [],

        // 费用选择数据
        selected_feiyong: [],

        // 收款方式
        radio_pay_way: '现金',

        // 选择的小区
        xiaoqu: '',
        // 费用总计
        total: 0.00,

        // 收款备注
        remark: ''

    }),
    components: {
        'shoukuandan': shoukuandanComponent,
    },
    mounted() {
        this.get_xiaoqu_list()
    },
    watch: {

        // 监控选中的房屋
        active(val) {
            this.init()
            if (val.length != 0) {
                console.log('当前选择的房屋ID为' + val[0]._id)
                this.get_feiyong_lists(val[0])

                // 获取历史记录
                this.get_history_items(val[0])
            }
        },

        // 监控选中的费用
        selected_feiyong: {
            handler: function (val) {
                this.total = 0
                val.forEach((item) => {
                    this.total = this.total + Number(item.zongji)
                })
            },
            deep: true // 深度监测
        },
    },
    methods: {

        // 清空 初始化
        init(){
            this.feiyong_items = [] // 清空费用列表
            this.selected_feiyong = [] // 清空选择的费用列表
            this.history_items = [] // 清空历史记录
            this.remark = '' // 清空备注
            this.text_field_yu_e = '' // 清空余额数据
        },

        // 获取项列表
        get_feixiang_lists(xiaoqu) {
            this.$axios.get('/feixiang/lists', { params: { xiaoqu: xiaoqu } })
                .then((res) => {
                    this.feixiang_items = res.data
                })
        },

        // 获取小区列表
        get_xiaoqu_list() {
            this.$axios.get('/xiaoqu/getlist')
                .then((res) => {
                    this.xiaoqu_items = res.data
                })
        },
        // 选择小区
        change_xiaoqu(val) {
            this.get_loudong_list(val) // 获取该小区下的楼栋列表
            this.get_feixiang_lists(val) // 获取该小区下的费项列表
            this.active = []
        },
        // 获取楼栋列表
        get_loudong_list(xiaoqu) {
            this.$axios.get('/loudong/get_loudong_danyuan', { params: { xiaoqu: xiaoqu } })
                .then((res) => {
                    // 给每个单元添加一个空的children数组
                    res.data.forEach((loudong_item) => {
                        loudong_item.children.forEach((danyuan_item) => {
                            danyuan_item.children = []
                        })
                    })
                    this.loudong_items = res.data
                })
        },

        // 获取单元子节点的房屋列表
        async fetchFangwus(item) {
            const pause = ms => new Promise(resolve => setTimeout(resolve, ms))
            await pause(500)
            await this.$axios.get('fangwu/getlist', { params: { danyuan: item._id, have_wx_user: 1 } })
                .then((res) => {
                    item.children.push(...res.data)
                })
        },

        // 推送微信费用
        push() {
            this.overlay = true
            this.loading_shouqu = true
            this.$axios.post('/feiyong/push', { fangwu: this.active[0], remark: this.remark })
                .then((res) => {
                    setTimeout(() => {
                        this.loading_shouqu = false
                        this.overlay = false
                        this.snackbar = {
                            show: true,
                            text: res.data,
                            color: "success",
                        }
                        this.get_history_items(this.active[0])
                    }, 500);
                })
        },

        // 打印催款通知书
        // 打印
        print() {
            let LODOP = getLodop()//调用getLodop获取LODOP对象
            if (LODOP.error) {
                this.$refs.lodop_msg.innerHTML = LODOP.msg
            } else {
                LODOP.PRINT_INIT("")
                LODOP.SET_PRINT_MODE("PRINT_PAGE_PERCENT", "Auto-Width");
                LODOP.SET_PREVIEW_WINDOW(0, 0, 0);
                LODOP.SET_PRINT_PAGESIZE(1, 0, 0, "A4");
                LODOP.SET_SHOW_MODE("PREVIEW_NO_MINIMIZE", true); //预览窗口禁止最小化，并始终最前	
                LODOP.SET_PRINT_MODE("AUTO_CLOSE_PREWINDOW", 1); //打印后自动关闭预览窗口
                var strFormHtml = this.$refs.print.innerHTML;
                LODOP.ADD_PRINT_HTM(0, 10, "100%", "100%", strFormHtml);
                LODOP.PREVIEW();
            }
        },

        // 刷新
        refresh() {
            this.selected_feiyong = [] // 清空选择的费用列表
            this.history_items = []
            this.remark = '' // 清空备注
            this.text_field_yu_e = ''
            this.get_feiyong_lists(this.active[0])

            // 获取历史记录
            this.get_history_items(this.active[0])
        },

        // 获取费用列表
        get_feiyong_lists(fangwu) {
            this.loading_feiyong = true
            this.feiyong_items = []
            this.$axios.get('/feiyong/lists', { params: { fangwu: fangwu._id } })
                .then((res) => {
                    setTimeout(() => {
                        this.feiyong_items = res.data
                        this.loading_feiyong = false
                    }, 500);
                })
        },
        // 获取历史记录
        async get_history_items(fangwu) {
            this.history_items = []
            var history_arr = []
            this.loading_history = true
            const promise1 = this.$axios.get('/shoukuandan/lists', { params: { fangwu: fangwu._id } })
                .then((res) => {
                    var obj = {}
                    res.data.forEach((item) => {
                        obj = {
                            _id: item._id,
                            date: item.date_china,
                            info: '收取费用' + item.feiyong.length + '条，总计金额：' + item.total,
                            user: item.user,
                            shoukuandan: item
                        }
                        history_arr.push(obj)
                    })
                })
            const promise2 = this.$axios.get('/log/fangwu', { params: { fangwu: fangwu._id } })
                .then((res) => {
                    var obj = {}
                    res.data.forEach(item => {
                        obj = {
                            _id: item._id,
                            date: item.date_china,
                            info: item.info,
                            user: item.user
                        }
                        history_arr.push(obj)
                    })
                })
            await Promise.all([promise1, promise2]).then(() => {
                // 将数组中的对象进行排序
                setTimeout(() => {
                    history_arr.sort((a, b) => {
                        return new Date(b.date) - new Date(a.date)
                    })
                    this.loading_history = false
                    this.history_items = history_arr
                }, 500);
            })
        },

        // 换表
        btn_change_biao() {
            // 新增一条费用(传入该费用对象)，并且该费用的change_time+1
            this.$axios.post('/feiyong/change_biao', this.selected_feiyong[0])
                .then((res) => {
                    this.selected_feiyong = []
                    this.get_feiyong_lists(this.active[0])
                    this.get_history_items(this.active[0])
                    this.snackbar = {
                        show: true,
                        text: res.data.message,
                        color: "success",
                    }
                })
        },

        // 删除选择的费用
        btn_del() {
            this.$axios.post('/feiyong/delete', this.selected_feiyong)
                .then((res) => {
                    this.snackbar = {
                        show: true,
                        text: res.data.message,
                        color: "success",
                    }
                    this.selected_feiyong = []
                    this.get_feiyong_lists(this.active[0])
                })
        },

        // 新增费项
        create() {
            this.$axios.post('/feiyong/create', { feixiang_obj_arr: this.check_feixiang, fangwu_obj: this.active[0] })
                .then((res) => {
                    this.check_feixiang = []
                    this.dialog = false
                    this.snackbar = {
                        show: true,
                        text: res.data.message,
                        color: "success",
                    }
                    this.get_feiyong_lists(this.active[0])
                })
                .catch((error) => {
                    console.log(error)
                })
        },
        // 字段编辑弹出框
        // 起数
        save_start(item) {
            if (item.feixiang.leixing == '按读表') {
                // js float 计算有误 2.11-1 = 1.1099999999999999
                item.shuliang = (item.end - item.start).toFixed(2)
                item.zongji = (item.shuliang * item.danjia * item.zhouqi_beishu).toFixed(2)
            } else {
                var start_date = new Date(item.start)
                item.end = this.formatDate(this.addMonthDate(start_date, Number(item.zhouqi_beishu)))
            }

            // 更新数据库
            this.$axios.post('/feiyong/update', item)
                .then((res) => {
                    this.snackbar = {
                        show: true,
                        text: res.data.message,
                        color: "success",
                    }
                })
        },

        // 止数
        save_end(item) {
            // js float 计算有误 2.11-1 = 1.1099999999999999
            item.shuliang = (item.end - item.start).toFixed(2)
            item.zongji = (item.shuliang * item.danjia * item.zhouqi_beishu).toFixed(2)

            // 更新数据库
            this.$axios.post('/feiyong/update', item)
                .then((res) => {
                    this.snackbar = {
                        show: true,
                        text: res.data.message,
                        color: "success",
                    }
                })
        },

        // 周期/倍数
        save_zhouqi_beishu(item) {
            if (item.feixiang.leixing == '按读表' || item.feixiang.leixing == '临时费') {
                item.zongji = (item.shuliang * item.danjia * item.zhouqi_beishu).toFixed(2)
            } else {
                item.zhouqi_beishu = Math.trunc(Number(item.zhouqi_beishu))
                var start_date = new Date(item.start)
                item.end = this.formatDate(this.addMonthDate(start_date, Number(item.zhouqi_beishu)))
                item.zongji = (item.shuliang * item.danjia * item.zhouqi_beishu).toFixed(2)
            }
            // 更新数据库
            this.$axios.post('/feiyong/update', item)
                .then((res) => {
                    this.snackbar = {
                        show: true,
                        text: res.data.message,
                        color: "success",
                    }
                })
        },

        // 数量编辑弹出框
        save(item) {

            item.zongji = (Number(item.shuliang) * item.danjia * item.zhouqi_beishu).toFixed(2)

            // 更新数据库
            this.$axios.post('/feiyong/update', item)
                .then((res) => {
                    this.snackbar = {
                        show: true,
                        text: res.data.message,
                        color: "success",
                    }
                })
        },
        cancel() {
            this.snackbar = {
                show: true,
                text: '取消',
                color: "error",
            }
        },
        open() {
            console.log('Dialog opened')
        },
        close() {
            console.log('Dialog closed')
        },

        // 修改余额
        btn_modify_yu_e() {
            if (this.text_field_yu_e) {
                this.active[0].yu_e = (Number(this.active[0].yu_e) + Number(this.text_field_yu_e)).toFixed(2) // js float 计算有误 2.11-1 = 1.1099999999999999
                this.$axios.post('/fangwu/update/yu_e', this.active[0])
                    .then((res) => {
                        this.snackbar = {
                            show: true,
                            text: res.data.message,
                            color: "success",
                        }
                        this.text_field_yu_e = ''
                        this.get_history_items(this.active[0])
                    })
            } else {
                this.snackbar = {
                    show: true,
                    text: '余额操作数不能为空',
                    color: "error",
                }
            }
        },
        // 确认收取--------------------------------------------
        btn_ok() {
            this.loading_shouqu = true
            // 打开遮罩层
            this.overlay = true
            var shoukuandan_obj = {
                remark: this.remark, //备注
                total: this.total.toFixed(2), //总计费用
                feiyong: this.selected_feiyong, //费用列表
                fangwu: this.active[0], //房屋
                pay_way: this.radio_pay_way, //收款方式
                youhui: this.youhui, //优惠金额
            }
            console.log(shoukuandan_obj)
            this.$axios.post('/shoukuandan/create', shoukuandan_obj)
                .then((res) => {
                    this.youhui = ''
                    this.remark = ''
                    this.selected_feiyong = []

                    // 获取费用列表
                    this.get_feiyong_lists(this.active[0])
                    this.shoukuandan_id = res.data.shoukuandan_doc._id
                    // 获取历史记录
                    this.get_history_items(this.active[0])

                    setTimeout(() => {
                        // 关闭遮罩层
                        this.overlay = false
                        this.loading_shouqu = false
                        // 展示收款单
                        this.shoukuandan_dialog = true
                    }, 2000);
                })
        },

        // 日期增加月份
        addMonthDate(date, month) {
            // 加费项周期月份
            date.setMonth(date.getMonth() + month)
            // 减去一天
            date.setDate(date.getDate() - 1)
            return date
        },

        // 格式化日期为YYYY-MM-DD
        formatDate(date) {
            var y = date.getFullYear();
            var m = date.getMonth() + 1;
            m = m < 10 ? '0' + m : m;
            var d = date.getDate();
            d = d < 10 ? ('0' + d) : d;
            return y + '-' + m + '-' + d;
        }
    }
}
</script>
<style>
.logs {
    height: 100%;
    overflow: auto;
    width: 50%;
    background: url('@/assets/null.png') no-repeat center;
}
</style>