<template>
    <v-card height="100%" style="flex:1;overflow: auto;">
        <v-card-title>
            操作日志
        </v-card-title>
        <v-card-text>
            <v-data-table :headers="headers" :items="items" :options.sync="options" :server-items-length="total_num"
                :loading="loading">
                <template v-slot:top>
                </template>
                <template v-slot:[`item.date`]="{ item }">
                    {{ new Date(item.date).toLocaleString() }}
                </template>
            </v-data-table>
        </v-card-text>
    </v-card>
</template>
<script>
export default {
    data: () => ({
        loading: false,
        total_num: 0,
        options: {},
        headers: [
            { text: '时间', sortable: false, value: 'date' },
            { text: '小区', sortable: false, value: 'xiaoqu.name' },
            { text: '房屋', sortable: false, value: 'fangwu.name' },
            { text: '账号', sortable: false, value: 'user.username', },
            { text: '姓名', sortable: false, value: 'user.nickname' },
            { text: '事件', sortable: false, value: 'info' },
        ],
        items: []
    }),

    watch: {
        options: {
            handler() {
                this.getlist()
            },
            deep: true,
        },
    },

    methods: {
        getlist() {
            this.loading = true
            this.$axios.get('/log', { params: { page: this.options.page, itemsPerPage: this.options.itemsPerPage } })
                .then((res) => {
                    setTimeout(() => {
                        this.loading = false
                        this.items = res.data.log_doc
                        this.total_num = res.data.total_num
                    }, 500);
                })
        }
    }
}
</script>