<template>
    <v-card style="height:100%">
        <v-card-text ref="chart" style="height:100%">

        </v-card-text>
    </v-card>
</template>
<script>
import * as echarts from 'echarts';
export default {
    props: ['echart_option'],
    data() {
        return {
            chart: null
        }
    },
    watch: {
        echart_option: {
            handler: function (val) {
                console.log(val)
                this.drawer()
            },
            deep: true
        }
    },
    methods: {
        drawer() {
            if (this.chart == null || this.chart == "" || this.chart == undefined) {
                this.chart = echarts.init(this.$refs.chart)
            }
            var option = {
                title: {
                    text: new Date().getMonth() + 1 + '月缴费金额趋势',
                },
                legend: { top: 20 },
                tooltip: {
                    trigger: 'axis',
                    axisPointer: {
                        type: 'cross',
                        label: {
                            backgroundColor: 'red'
                        }
                    }
                },
                grid: {
                    left: '0%',
                    right: '0%',
                    bottom: '0%',
                    containLabel: true
                },
                xAxis: { data: this.echart_option.xAxis_data },
                yAxis: {},
                series: this.echart_option.series
            }
            this.chart.setOption(option,true);
            window.onresize = () => {
                this.chart.resize();
            };
        }
    }
};
</script>