<template>
    <v-row>
        <v-snackbar app top timeout="2000" :color="snackbar.color" v-model="snackbar.show">{{ snackbar.text }}
        </v-snackbar>
        <XiaoquLists @select_xiaoqu="select_xiaoqu"></XiaoquLists>
        <v-col cols="12" sm="9" md="9" style="height:100%" class="d-flex">
            <v-card height="100%" style="flex:1;overflow: auto;">
                <v-card-title>{{ xiaoqu ? xiaoqu.name : '' }} 报事报修列表
                    <v-spacer></v-spacer>
                    <v-dialog v-model="dialog" max-width="500px" persistent>
                        <v-card>
                            <v-card-title>
                                <span class="text-h5">回复</span>
                            </v-card-title>

                            <v-card-text>
                                <v-textarea v-model="reply_content" label="内容"
                                    value="The Woodman set to work at once, aned nearly through.">
                                </v-textarea>
                            </v-card-text>

                            <v-card-actions>
                                <v-spacer></v-spacer>
                                <v-btn color="blue darken-1" text @click="close" :loading="loading">
                                    取消
                                </v-btn>
                                <v-btn color="blue darken-1" text @click="save" :loading="loading">
                                    回复
                                </v-btn>
                            </v-card-actions>
                        </v-card>
                    </v-dialog>
                </v-card-title>
                <v-card-text>
                    <v-data-table :headers="headers" :items="report_items" item-key="_id" :loading="list_loading"
                        show-expand single-expand :options.sync="options" :server-items-length="total_num">
                        <template v-slot:[`item.date`]="{ item }">
                            {{ new Date(item.date).toLocaleString() }}
                        </template>
                        <template v-slot:[`item.status`]="{ item }">
                            <div v-if="item.reply_date" style="color:green">已处理</div>
                            <div v-else style="color:red">未处理</div>
                        </template>
                        <template v-slot:[`item.actions`]="{ item }">
                            <v-icon color="green" small class="mr-2" @click="replyItem(item)">
                                mdi-reply
                            </v-icon>
                        </template>
                        <template v-slot:expanded-item="{ headers, item }">
                            <td :colspan="headers.length">
                                <v-row style="padding:15px 0">
                                    <v-col v-for="img_url in item.images" :key="img_url" class="d-flex child-flex"
                                        cols="4">
                                        <v-img :src="'https://api.dayangtong.cn:8080/' + img_url" aspect-ratio="1">
                                            <template v-slot:placeholder>
                                                <v-row class="fill-height ma-0" align="center" justify="center">
                                                    <v-progress-circular indeterminate>
                                                    </v-progress-circular>
                                                </v-row>
                                            </template>
                                        </v-img>
                                    </v-col>
                                </v-row>
                                <p>报事报修内容：{{ item.content }}</p>
                                <p v-if="item.reply_user && item.reply_content">{{ new
                                        Date(item.reply_date).toLocaleString()
                                }} @
                                    {{ item.reply_user.nickname }} : {{ item.reply_content }}</p>
                            </td>
                        </template>
                    </v-data-table>
                </v-card-text>
            </v-card>
        </v-col>
    </v-row>
</template>
<script>
import XiaoquLists from "@/views/components/xiaoqu-lists.vue";
export default {
    components: {
        XiaoquLists
    },
    data: () => ({
        snackbar: {
            show: false,
            text: "",
            color: "success",
        },

        reply_item: {},
        reply_content: '',
        dialog: false,
        loading: false,
        list_loading: false,
        total_num: 0,
        options: {},
        xiaoqu: null,
        // 费项列表
        report_items: [],
        headers: [
            { text: "时间", sortable: true, value: "date" },
            { text: "楼栋", sortable: false, value: "loudong.name" },
            { text: "单元", sortable: false, value: "danyuan.name" },
            { text: "房屋", sortable: false, value: "fangwu.name" },
            { text: "状态", sortable: false, value: "status" },
            { text: "操作", sortable: false, value: "actions" },
            { value: "data-table-expand" },
        ],
    }),
    watch: {
        dialog(val) {
            val || this.close()
        },
        options: {
            handler() {
                if (this.xiaoqu) {
                    this.get_report_list()
                }
            },
            deep: true,
        },
    },
    methods: {
        // 子组件事件 选择小区
        select_xiaoqu(xiaoqu) {
            this.xiaoqu = xiaoqu
            if (xiaoqu) {
                this.get_report_list()
            } else {
                this.report_items = []
            }
        },

        // 获取公告列表
        get_report_list() {
            this.list_loading = true
            this.$axios.get("/report", { params: { xiaoqu: this.xiaoqu._id, page: this.options.page, itemsPerPage: this.options.itemsPerPage } })
                .then((res) => {
                    setTimeout(() => {
                        this.list_loading = false
                        this.report_items = res.data.report_doc
                        this.total_num = res.data.total_num
                    }, 500);
                })
        },

        close() {
            this.dialog = false
            this.loading = false
            this.reply_item = {}
            this.reply_content = ''
        },
        // 保存到数据库---------------------------------------------------------------
        save() {
            if (this.reply_content == '') {
                this.snackbar = {
                    show: true,
                    text: "不允许为空",
                    color: "error",
                }
                return
            }
            this.loading = true
            this.reply_item.reply_content = this.reply_content
            this.reply_item.reply_date = new Date()
            this.$axios.post("/report/reply", this.reply_item)
                .then((res) => {
                    console.log(res)
                    this.close()
                    this.get_report_list()
                })
        },
        replyItem(item) {
            this.dialog = true
            this.reply_item = item
        }
    },
};
</script>
