<template>
    <v-row>
        <v-snackbar app top timeout="2000" :color="snackbar.color" v-model="snackbar.show">{{ snackbar.text }}
        </v-snackbar>
        <XiaoquLists @select_xiaoqu="select_xiaoqu"></XiaoquLists>
        <v-col cols="12" sm="9" md="9">
            <v-card min-height="100%">
                <v-card-title>{{ xiaoqu ? xiaoqu.name : '' }} 公告列表
                    <v-spacer></v-spacer>
                    <v-dialog v-model="dialog" max-width="500px" persistent>
                        <template v-slot:activator="{ on, attrs }">
                            <v-btn color="primary" class="mb-2" v-bind="attrs" v-on="on"
                                :disabled="xiaoqu ? false : true">
                                新增
                            </v-btn>
                        </template>
                        <v-card>
                            <v-card-title>
                                <span class="text-h5">发布</span>
                            </v-card-title>

                            <v-card-text>
                                <v-text-field v-model="notice_title" label="标题"></v-text-field>
                                <v-textarea v-model="notice_content" label="内容">
                                </v-textarea>
                                <v-progress-linear striped :value="finish" height="25">
                                    <strong>{{ Math.ceil(finish) }}%</strong>
                                </v-progress-linear>
                            </v-card-text>

                            <v-card-actions>
                                <v-spacer></v-spacer>
                                <v-btn color="blue darken-1" text @click="close" :loading="loading">
                                    取消
                                </v-btn>
                                <v-btn color="blue darken-1" text @click="save" :loading="loading">
                                    保存
                                </v-btn>
                            </v-card-actions>
                        </v-card>
                    </v-dialog>
                </v-card-title>
                <v-card-text>
                    <v-data-table :headers="headers" :items="notice_items" item-key="_id" :loading="table_loading"
                        :options.sync="options" :server-items-length="total_num">
                        <template v-slot:[`item.date`]="{ item }">
                            {{ new Date(item.date).toLocaleString() }}
                        </template>
                        <template v-slot:[`item.actions`]="{ item }">
                            <v-icon color="error" small class="mr-2" @click="deleteItem(item)">
                                mdi-delete
                            </v-icon>
                        </template>
                    </v-data-table>
                </v-card-text>
            </v-card>
        </v-col>
    </v-row>
</template>
<script>
import XiaoquLists from "@/views/components/xiaoqu-lists.vue";
export default {
    components: {
        XiaoquLists
    },
    data: () => ({
        snackbar: {
            show: false,
            text: "",
            color: "success",
        },
        total_num: 0,
        options: {},
        notice_title: '',
        notice_content: '',
        dialog: false,
        loading: false,
        table_loading: false,
        finish: 0,
        xiaoqu: null,
        // 费项列表
        notice_items: [],
        headers: [
            { text: "时间", sortable: false, value: "date" },
            { text: "标题", sortable: true, value: "title" },
            { text: "内容", sortable: false, value: "content" },
            { text: "操作", sortable: false, value: "actions" },
        ],
    }),
    watch: {
        dialog(val) {
            val || this.close()
        },
        options: {
            handler() {
                if (this.xiaoqu) {
                    this.get_notice_list()
                }
            },
            deep: true,
        },
    },
    methods: {
        // 子组件事件 选择小区
        select_xiaoqu(xiaoqu) {
            this.xiaoqu = xiaoqu
            if (xiaoqu) {
                this.get_notice_list()
            } else {
                this.notice_items = []
            }
        },

        // 获取公告列表
        get_notice_list() {
            this.table_loading = true
            this.$axios.get("/notice", { params: { xiaoqu: this.xiaoqu._id, page: this.options.page, itemsPerPage: this.options.itemsPerPage } })
                .then((res) => {
                    setTimeout(() => {
                        this.table_loading = false
                        this.notice_items = res.data.notice_doc
                        this.total_num = res.data.total_num
                    }, 500);
                })
        },

        close() {
            this.dialog = false
            this.notice_title = ''
            this.notice_content = ''
        },
        // 保存到数据库---------------------------------------------------------------
        save() {
            if (this.notice_title == '' || this.notice_content == '') {
                this.snackbar = {
                    show: true,
                    text: "不允许为空",
                    color: "error",
                }
                return
            }
            this.loading = true
            var notice_item = {
                xiaoqu: this.xiaoqu,
                title: this.notice_title,
                content: this.notice_content,
                date: new Date()
            }
            this.$axios.post("/notice", notice_item)
                .then((res) => {
                    // 循环查询进度
                    var fun = setInterval(() => {
                        this.$axios.post("/notice/finish", res.data)
                            .then((res) => {
                                this.finish = res.data.finish
                                console.log(this.finish)
                                if (this.finish == 100) {
                                    clearInterval(fun)
                                    setTimeout(() => {
                                        this.dialog = false
                                        this.finish = 0
                                        this.loading = false
                                        this.get_notice_list()
                                    }, 500);
                                }
                            })
                    }, 1000);
                })
        },

        // 删除公告
        deleteItem(item) {
            this.$axios.post("/notice/del", item)
                .then((res) => {
                    this.get_notice_list()
                    this.snackbar = {
                        show: true,
                        text: res.data,
                        color: 'success'
                    }
                })
        }
    },
};
</script>
