<template>
  <v-main>
    <v-snackbar app top timeout="2000" :color="snackbar.color" v-model="snackbar.show">{{ snackbar.text }}</v-snackbar>
    <v-container style="height: 100%;" class="d-flex align-center">
      <v-row class="justify-center">
        <v-col cols="12" sm="6" md="6" lg="3">
          <v-card elevation="12" class="pa-4">

            <!-- 登录 -->
            <template v-if="logining">
              <v-card-title class="flex-column justify-center">
                <!-- <div><img src="@/assets/logo.png"></div> -->
                <!-- <div style="color:#222;font-size:4vh;padding:1vh;font-family: fantasy;line-height: 1;">
                  <span style="color:#1976d2">D</span><span>a</span><span
                    style="color:#1976d2">Y</span><span>ang</span><span style="color:#1976d2">T</span><span>ong</span>
                </div> -->
                <div style="color:#222;">
                  <h2 style="line-height: 2;">
                  <span>物业</span>
                  <span style="color:#1976d2">电子台账</span>
                  <span>系统</span>
                </h2>
                </div>
              </v-card-title>
              <v-card-subtitle class="text-center mb-4"> ── 助力现代化物业发展，让管理更规范、更安全 ── </v-card-subtitle>
              <v-card-text>
                <v-text-field autofocus clearable v-model="form_login.username" outlined
                  prepend-inner-icon="mdi-account" label="账号">
                </v-text-field>
                <v-text-field hide-details @click:append="password_show = !password_show"
                  :type="password_show ? 'text' : 'password'" :append-icon="password_show ? 'mdi-eye' : 'mdi-eye-off'"
                  v-model="form_login.password" outlined prepend-inner-icon="mdi-key" label="密码">
                </v-text-field>

              </v-card-text>
            </template>

            <!-- 注册 -->
            <template v-else>
              <v-card-text>
                <p class="red--text"><span
                    class="font-weight-bold">通知：</span>由于近期注册用户较多，且部分用户存在恶意注册行为，目前暂时关闭账号自助注册功能，您可以微信扫码联系平台客服，由客服人员为您免费注册账号，同时指导您使用本系统！
                </p>
                <div class="text-center"><img style="width:100%" src="@/assets/kefu.jpg"></div>
              </v-card-text>
            </template>

            <v-card-actions>
              <v-spacer></v-spacer>
              <template v-if="logining">
                <v-btn color="primary" @click="login">
                  登 录
                </v-btn>
                <v-btn color="primary" @click="regist" text>
                  注 册
                </v-btn>
              </template>
              <template v-else>
                <v-btn color="primary" @click="goback">
                  返回登录
                </v-btn>
              </template>
            </v-card-actions>
          </v-card>
        </v-col>
      </v-row>
    </v-container>
    <v-overlay :value="loading">
      <v-progress-circular indeterminate size="64" color="primary"></v-progress-circular>
    </v-overlay>
  </v-main>
</template>

<script>
export default {
  name: "LoginView",
  data: () => {
    return {
      password_show: false,
      snackbar: {
        show: false,
        text: "",
        color: "success"
      },
      // 登录窗口
      logining: true,
      loading: false,
      form_login: {
        username: '',
        password: ''
      }
    }
  },
  methods: {
    // 登录
    login() {
      if (!this.form_login.username || !this.form_login.password) {
        this.snackbar = {
          show: true,
          text: "用户名和密码不能为空！",
          color: "error"
        }
      } else {
        this.loading = true
        this.$axios.post('/user/login', this.form_login)
          .then(res => {
            console.log(res.data)
            if (res.data.token) {
              sessionStorage.setItem('token', res.data.token)
              sessionStorage.setItem('userid', res.data.userid)
              sessionStorage.setItem('nickname', res.data.nickname)
              sessionStorage.setItem('company', JSON.stringify(res.data.company))
              setTimeout(() => {
                this.loading = false
                this.$router.push('/home')
              }, 2000);
            } else {
              setTimeout(() => {
                this.loading = false
                this.snackbar = {
                  show: true,
                  text: res.data.message,
                  color: res.data.errors ? "error" : "success",
                }
              }, 1000);
            }
          })
          .catch(error => {
            this.snackbar = {
              show: true,
              text: error,
              color: "error"
            }
            this.loading = false
          })
      }
    },
    // 注册
    regist() {
      this.logining = false
    },
    // 提交注册
    goback() {
      this.logining = true
    }
  }
};
</script>
<style scoped>
.v-main {
  background: url(@/assets/login_bg.jpg);
}
</style>