<template>
    <v-row>
        <v-col cols="12" sm="12" md="12" style="height:100%">
            <v-card height="100%" style="overflow: auto;">
                <v-card-title>用户列表</v-card-title>
                <v-card-text>
                    <!-- 表格 -->
                    <v-data-table :headers="headers" :items="user_items" item-key="_id" :loading="table_loading"
                        :options.sync="options" :server-items-length="total_num" height="66vh">
                    </v-data-table>
                </v-card-text>
            </v-card>
        </v-col>
    </v-row>
</template>
<script>
export default {
    data: () => ({
        options: {},
        total_num: 0,
        table_loading: false,
        // 用户列表
        user_items: [],
        headers: [
            { text: "小程序", sortable: false, value: "app[0].authorizer_info.nick_name" },
            { text: "小程序appid", sortable: false, value: "appid" },
            { text: "用户openid", sortable: false, value: "openid" },
            { text: "姓名", sortable: false, value: "nickname" },
            { text: "身份证", sortable: false, value: "idcard" },
            { text: "电话", sortable: false, value: "phone" },
            { text: "小区", sortable: false, value: "xiaoqu[0].name" },
        ],
    }),
    watch: {
        options: {
            handler() {
                this.get_user_lists()
            },
            deep: true,
        },
    },
    methods: {
        // 获取业主列表
        get_user_lists() {
            this.table_loading = true
            this.$axios.get("/wx_user/allusers", { params: { page: this.options.page, itemsPerPage: this.options.itemsPerPage } })
                .then((res) => {
                    setTimeout(() => {
                        this.table_loading = false
                        this.user_items = res.data.user_doc
                        this.total_num = res.data.total_num
                    }, 500);
                })
        }
    },
};
</script>
