<template>
    <v-row>
        <XiaoquLists @select_xiaoqu="select_xiaoqu"></XiaoquLists>
        <v-col cols="12" sm="9" md="9" style="height:100%">
            <v-card height="100%" style="overflow: auto;">

                <v-card-title class="flex-nowrap">
                    <!-- 费项下拉框 -->
                    <v-select :disabled="xiaoqu ? false : true" :items="feixiang_items" label="费项" item-text="mingcheng"
                        item-value="_id" v-model="feixiang_selected" outlined dense hide-details class="mx-5"></v-select>
                    <v-spacer></v-spacer>
                    <!-- 楼栋下拉框 -->
                    <v-select :disabled="xiaoqu ? false : true" :items="loudong_items" label="楼栋" item-text="name"
                        item-value="_id" v-model="loudong_selected" outlined dense hide-details class="mx-5"
                        @change="change_loudong"></v-select>
                    <v-spacer></v-spacer>
                    <!-- 单元下拉框 -->
                    <v-select :disabled="loudong_selected != 0 ? false : true" :items="danyuan_items" label="单元"
                        item-text="name" item-value="_id" v-model="danyuan_selected" outlined dense hide-details
                        class="mx-5"></v-select>
                    <v-spacer></v-spacer>
                    <!-- 是否缴费下拉框 -->
                    <v-select :items="status_items" label="类型" v-model="status_selected" outlined dense hide-details
                        class="mx-5"></v-select>
                </v-card-title>

                <v-card-title>
                    {{ xiaoqu ? xiaoqu.name : '' }} 台账清单
                    <v-spacer></v-spacer>
                    <v-btn color="primary" :disabled="!xiaoqu" small class="ml-5" @click="search" :loading="loading">
                        查询
                    </v-btn>
                    <v-btn color="error" :disabled="feiyong_selected.length == 0" small class="ml-5" @click="btn_del">
                        删除
                    </v-btn>
                </v-card-title>

                <v-card-text>
                    <v-data-table v-model="feiyong_selected" :headers="headers" :items="feiyong_items" item-key="_id"
                        :options.sync="options" :show-select="showselect" class="elevation-0" :loading="loading"
                        :server-items-length="totalRecord" disable-sort height="65vh">
                    </v-data-table>
                </v-card-text>
            </v-card>
        </v-col>
    </v-row>
</template>
<script>
import XiaoquLists from "@/views/components/xiaoqu-lists.vue";
export default {
    components: {
        XiaoquLists
    },
    data: () => ({
        xiaoqu: null,

        options: {},
        totalRecord: 0,
        loading: false,
        // 下拉框数组
        feixiang_items: [{ _id: 0, mingcheng: '全部' }],
        loudong_items: [{ _id: 0, name: '全部' }],
        danyuan_items: [{ _id: 0, name: '全部' }],
        status_items: [{ value: 0, text: '未缴费' },{ value: 1, text: '已缴费' },{ value: 2, text: '已撤销' }],


        // 下拉框选择项
        feixiang_selected: 0,
        loudong_selected: 0,
        danyuan_selected: 0,
        status_selected: 0,

        // 费用台账选择项
        feiyong_selected: [],
        showselect: false,

        headers: [
            { text: '楼栋', value: 'loudong.name', },
            { text: '单元', value: 'danyuan.name' },
            { text: '房屋', value: 'fangwu.name' },
            { text: "名称", value: "mingcheng" },
            { text: "单价", value: "danjia" },
            { text: "起数", value: "start" },
            { text: "止数", value: "end" },
            { text: "周期/倍数", value: "zhouqi_beishu" },
            { text: "数量", value: "shuliang" },
            { text: "总计", value: "zongji" },
        ],
        // 费用台账数组列表
        feiyong_items: []
    }),
    watch: {
        options: {
            handler() {
                this.search()
            },
            deep: true,
        },
    },
    methods: {

        // 子组件事件 选择小区
        select_xiaoqu(xiaoqu) {
            // 清除
            this.feixiang_items = [{ _id: 0, mingcheng: '全部' }]
            this.feixiang_selected = 0
            this.loudong_items = [{ _id: 0, name: '全部' }]
            this.loudong_selected = 0
            this.danyuan_items = [{ _id: 0, name: '全部' }]
            this.danyuan_selected = 0

            this.feiyong_items = []
            this.feiyong_selected = []
            this.showselect = false

            this.xiaoqu = xiaoqu
            if (xiaoqu) {
                // 获取费项列表
                this.$axios.get('/feixiang/lists', { params: { xiaoqu: xiaoqu._id } })
                    .then((res) => {
                        this.feixiang_items = [{ _id: 0, mingcheng: '全部' }, ...res.data]
                    })

                // 获取楼栋列表
                this.$axios.get("/loudong/getlist", { params: { xiaoqu: xiaoqu._id } })
                    .then((res) => {
                        this.loudong_items = [{ _id: 0, name: '全部' }, ...res.data]
                    })
            }
        },

        // 选择楼栋
        change_loudong() {
            this.danyuan_items = [{ _id: 0, name: '全部' }]
            this.danyuan_selected = 0
            if (this.loudong_selected != 0) {
                this.$axios.get('/danyuan/getlist', { params: { loudong: this.loudong_selected } })
                    .then((res) => {
                        this.danyuan_items = [{ _id: 0, name: '全部' }, ...res.data]
                    })
            }
        },

        // 查询
        search() {
            this.loading = true
            this.feiyong_selected = []
            if (this.status_selected == 0) {
                this.showselect = true
            } else {
                this.showselect = false
            }
            var post_data = {
                xiaoqu: this.xiaoqu,
                feixiang: this.feixiang_selected,
                loudong: this.loudong_selected,
                danyuan: this.danyuan_selected,
                status: this.status_selected,

                // 第几页
                page: this.options.page,
                // 每页条数
                itemsPerPage: this.options.itemsPerPage,
            }
            this.$axios.post('/taizhang', post_data)
                .then((res) => {
                    setTimeout(() => {
                        console.log(res.data)
                        this.loading = false
                        this.feiyong_items = res.data.doc
                        this.totalRecord = res.data.totalRecord
                    }, 500);
                })
        },

        // 删除选择的费用
        btn_del() {
            this.$axios.post('/feiyong/delete', this.feiyong_selected)
                .then((res) => {
                    console.log(res)
                    this.feiyong_selected = []
                    this.search()
                })
        },
    },
};
</script>
