import weixinUserView from '../views/weixin/userView.vue'
import weixinOrderView from '../views/weixin/orderView.vue'
import weixinAuthorizerView from '../views/weixin/authorizerView.vue'
import layoutView from '../views/layoutView.vue'

const weixin_routes = {
  path: '/weixin',
  component: layoutView,
  children: [
    {
      path: '/weixin/users',
      name: 'weixinuser',
      component: weixinUserView
    },{
      path: '/weixin/orders',
      name: 'weixinorder',
      component: weixinOrderView
    },{
      path: '/weixin/authorizer',
      name: 'weixinauthorizer',
      component: weixinAuthorizerView
    }
  ]
}

export default weixin_routes