<template>
    <v-row>
        <XiaoquLists @select_xiaoqu="select_xiaoqu"></XiaoquLists>
        <v-col cols="12" sm="9" md="9">
            <v-card min-height="100%">
                <v-card-title>{{ xiaoqu ? xiaoqu.name : '' }} 点位管理
                    <v-spacer></v-spacer>
                    <v-btn :disabled="xiaoqu ? false : true" @click="add_sheet" color="primary">
                        新增点位
                    </v-btn>
                    <v-dialog v-model="dialog" max-width="500px" persistent>
                        <v-card>
                            <v-card-title>
                                {{ dialog_title }}
                            </v-card-title>
                            <v-card-text>
                                <v-form ref="form">
                                    <v-text-field autofocus label="点位名称" v-model="edit_qrcode.qrcode_name"
                                        :rules="[value => !!value || '必填']">
                                    </v-text-field>
                                </v-form>
                            </v-card-text>
                            <v-card-actions>
                                <v-spacer></v-spacer>
                                <v-btn color="blue darken-1" text @click="close">
                                    取消
                                </v-btn>
                                <v-btn color="blue darken-1" text @click="save">
                                    确定
                                </v-btn>
                            </v-card-actions>
                        </v-card>
                    </v-dialog>
                </v-card-title>
                <v-card-text>
                    <v-data-table :headers="headers" :items="sheet_items" item-key="_id">
                        <template v-slot:[`item.action`]="{ item }">
                            <v-btn @click="edit_sheet(item)" text small color="primary">
                                编辑
                            </v-btn>
                            <v-btn @click="del_sheet(item)" text small color="red">
                                删除
                            </v-btn>
                            <v-tooltip right>
                                <template v-slot:activator="{ on, attrs }">
                                    <v-btn color="success" text small v-bind="attrs" v-on="on" @click="download(item)">
                                        下载二维码
                                    </v-btn>
                                </template>
                                <div ref="qrcode" style="background:#1976d2 ;padding:20px;text-align: center;">
                                    <img :src="item.qrcode_img_src">
                                    <div style="color:#fff;font-size: large;font-weight: bold;line-height: 2;">
                                        {{ xiaoqu.name }}巡逻点</div>
                                    <div style="color:#fff;line-height: 2;">{{ item.qrcode_name }}</div>
                                </div>
                            </v-tooltip>
                        </template>
                    </v-data-table>
                </v-card-text>
            </v-card>
        </v-col>
    </v-row>
</template>
<script>
import XiaoquLists from "@/views/components/xiaoqu-lists.vue";
import QRCode from "qrcode";
import html2canvas from 'html2canvas';
export default {
    components: {
        XiaoquLists
    },
    data: () => ({
        xiaoqu: null,
        dialog: false,
        dialog_title: '',
        edit_qrcode: {
            qrcode_name: '',
            xiaoqu: '',
        },
        sheet_items: [],
        headers: [
            { text: "名称", sortable: true, value: "qrcode_name" },
            { text: "操作", sortable: false, value: "action" },
        ],
    }),
    methods: {

        // 子组件事件 选择小区
        select_xiaoqu(xiaoqu) {
            this.xiaoqu = xiaoqu
            if (xiaoqu) {
                this.get_sheet_lists(xiaoqu._id)
            } else {
                this.sheet_items = []
            }
        },

        // 获取点位列表
        get_sheet_lists(xiaoqu_id) {
            this.$axios.get('/qrcode/sheet/lists', { params: { xiaoqu_id: xiaoqu_id } })
                .then(res => {
                    res.data.forEach(element => {
                        QRCode.toDataURL('https://api.dayangtong.cn:8080/' + this.xiaoqu.company.mini_appid + '?sheet=' + element._id, function (err, url) {
                            element.qrcode_img_src = url
                        })
                    });
                    this.sheet_items = res.data
                })
        },

        // 打开对话框
        add_sheet() {
            this.dialog = true
            this.dialog_title = '新增'
            this.edit_qrcode.xiaoqu = this.xiaoqu._id
        },
        // 关闭对话框
        close() {
            this.dialog = false
            this.edit_qrcode = {
                qrcode_name: '',
                xiaoqu: '',
            }
        },

        // 保存点位
        save() {
            if (this.$refs.form.validate()) {
                if (this.dialog_title == '新增') {
                    this.$axios.post('/qrcode/sheet/create', this.edit_qrcode)
                        .then(res => {
                            console.log(res)
                            this.dialog = false
                            this.get_sheet_lists(this.xiaoqu._id)
                        })
                } else {
                    this.$axios.post('/qrcode/sheet/updata', this.edit_qrcode)
                        .then(res => {
                            console.log(res)
                            this.dialog = false
                            this.get_sheet_lists(this.xiaoqu._id)
                        })
                }
            }
        },

        // 编辑点位
        edit_sheet(item) {
            this.dialog = true
            this.edit_qrcode = Object.assign({}, item)
            this.dialog_title = '编辑'
        },

        // 删除点位
        del_sheet(item) {
            this.$axios.post('/qrcode/sheet/del', item)
                .then(res => {
                    console.log(res)
                    this.get_sheet_lists(this.xiaoqu._id)
                })
        },

        download(item) {
            let a = document.createElement('a')
            a.setAttribute('download', this.xiaoqu.name + '-' + item.qrcode_name)
            html2canvas(this.$refs.qrcode).then(canvas => {
                a.href = canvas.toDataURL()
                a.click()
            })
        }
    },
};
</script>
<style>
.v-tooltip__content {
    padding: 1px;
}
</style>
