<template>
    <v-row>
        <v-snackbar app top timeout="2000" :color="snackbar.color" v-model="snackbar.show">{{ snackbar.text }}
        </v-snackbar>
        <XiaoquLists @select_xiaoqu="select_xiaoqu"></XiaoquLists>
        <v-col cols="12" sm="9" md="9">
            <v-card min-height="100%">
                <v-card-title>{{ xiaoqu ? xiaoqu.name : '' }} 访客记录列表
                    <v-spacer></v-spacer>
                    <v-btn color="primary" @click="download_qrcode" :disabled="xiaoqu ? false : true">
                        访客二维码下载
                    </v-btn>
                    <v-dialog v-model="dialog" max-width="300px" persistent>
                        <v-card>
                            <v-card-title>
                                下载访客二维码
                            </v-card-title>

                            <v-card-text>
                                <div ref="qrcode"
                                    style="background:#1976d2 ;padding:20px;text-align: center;border-radius: 4px;">
                                    <div style="color:#fff;font-size: large;font-weight: bold;line-height: 3;">
                                        {{ xiaoqu ? xiaoqu.name : '' }}访客登记</div>
                                    <img :src="qrcode_img_src" :alt="qrcode_url">
                                </div>

                            </v-card-text>

                            <v-card-actions>
                                <v-spacer></v-spacer>
                                <v-btn color="blue darken-1" text @click="close">
                                    取消
                                </v-btn>
                                <v-btn color="blue darken-1" text @click="download">
                                    下载
                                </v-btn>
                            </v-card-actions>
                        </v-card>
                    </v-dialog>
                </v-card-title>
                <v-card-text>
                    <v-data-table :headers="headers" :items="items" item-key="_id" :loading="loading"
                        :options.sync="options" :server-items-length="total_num">
                        <template v-slot:[`item.date`]="{ item }">
                            {{ new Date(item.date).toLocaleString() }}
                        </template>
                    </v-data-table>
                </v-card-text>
            </v-card>
        </v-col>
    </v-row>
</template>
<script>
import XiaoquLists from "@/views/components/xiaoqu-lists.vue";
import QRCode from "qrcode";
import html2canvas from 'html2canvas';
export default {
    components: {
        XiaoquLists
    },
    data: () => ({
        snackbar: {
            show: false,
            text: "",
            color: "success",
        },
        dialog: false,
        loading: false,
        total_num: 0,
        options: {},
        items: [],
        xiaoqu: null,
        headers: [
            { text: "时间", sortable: true, value: "date" },
            { text: "姓名", sortable: false, value: "nickname" },
            { text: "电话", sortable: false, value: "phone" },
            { text: "身份证", sortable: false, value: "idcard" },
            { text: "车牌", sortable: false, value: "plate" },
            { text: "来访原因", sortable: false, value: "content" },
        ],
        qrcode_url: '',
        qrcode_img_src: ''
    }),
    watch: {
        dialog(val) {
            val || this.close()
        },
        options: {
            handler() {
                if (this.xiaoqu) {
                    this.get_visitor_lists(this.xiaoqu._id)
                }
            },
            deep: true,
        },
    },
    methods: {

        // 子组件事件 选择小区
        select_xiaoqu(xiaoqu) {
            this.xiaoqu = xiaoqu
            if (xiaoqu) {
                this.get_visitor_lists(xiaoqu._id)
            } else {
                this.items = []
            }
        },

        close() {
            this.qrcode_img_src = ''
            this.qrcode_url = ''
            this.dialog = false
        },
        // 获取来访人记录
        get_visitor_lists(xiaoqu_id) {
            this.loading = true
            this.$axios.get('/qrcode/record/get_visitor_lists', { params: { xiaoqu: xiaoqu_id, page: this.options.page, itemsPerPage: this.options.itemsPerPage } })
                .then((res) => {
                    res.data.doc.forEach(element => {
                        element.detail.forEach(element_detail => {
                            element[element_detail.name] = element_detail.value
                        })
                    });
                    setTimeout(() => {
                        this.loading = false
                        this.items = res.data.doc
                        this.total_num = res.data.total_num
                    }, 500);
                })
        },

        // 下载二维码
        download_qrcode() {
            this.qrcode_img_src = ''
            this.qrcode_url = ''
            this.dialog = true
            // 获取改小区所在的物业公司所绑定的小程序appid
            if (!this.xiaoqu.company.mini_appid || this.xiaoqu.company.mini_appid == '') {
                this.qrcode_url = '该企业未绑定小程序，无法绑定生成访客二维码，请联系管理员添加'
                return
            } else {
                var mini_appid = this.xiaoqu.company.mini_appid
                let _this = this

                this.qrcode_url = 'https://api.dayangtong.cn:8080/' + mini_appid + '?xiaoqu=' + this.xiaoqu._id
                // 生成普通二维码，微信扫码打开小程序
                QRCode.toDataURL(this.qrcode_url, function (err, url) {
                    _this.qrcode_img_src = url
                })
            }

        },
        download() {
            let a = document.createElement('a')
            a.setAttribute('download', this.xiaoqu.name + '访客登记')
            html2canvas(this.$refs.qrcode).then(canvas => {
                a.href = canvas.toDataURL()
                a.click()
            })
        }
    },
};
</script>
