import visitorView from '../views/qrcode/visitorView.vue'
import sheetView from '../views/qrcode/sheetView.vue'
import recordView from '../views/qrcode/recordView.vue'
import layoutView from '../views/layoutView.vue'

const qrcode_routes = {
  path: '/qrcode',
  component: layoutView,
  children: [
    {
      path: '/qrcode/visitor',
      name: 'qrcode_visitor',
      component: visitorView
    },
    {
      path: '/qrcode/sheet',
      name: 'qrcode_sheet',
      component: sheetView
    },
    {
      path: '/qrcode/record',
      name: 'qrcode_record',
      component: recordView
    },
  ]
}

export default qrcode_routes