import Vue from 'vue'
import VueRouter from 'vue-router'

import LoginView from '../views/LoginView.vue'
import layoutView from '../views/layoutView.vue'
import homeView from '../views/homeView.vue'
// import NotFoundView from '../views/NotFoundView.vue'
import xitongguanli_routes from './xitongguanli.js' //系统管理
import fangchanguanli_routes from './fangchanguanli.js'  //房产管理
import zhanghaoguanli_routes from './zhanghaoguanli.js'  //账号管理
import feiyongguanli_routes from './feiyongguanli.js' //费用管理
import yezhuguanli_routes from './yezhuguanli' //业主管理
import notice_routes from './notice' //通知公告
import report_routes from './report' //报事报修
import qrcode_routes from './qrcode' //访客登记+巡逻巡更
import weixin_routes from './weixin' //小程序管理

Vue.use(VueRouter)

const routes = [
  {
    path: '/login',
    name: 'login',
    component: LoginView
  },
  {
    path: '/',
    name: 'layoutView',
    component: layoutView,
    redirect: '/home',
    children:[
      ...[{ path: '/home', name: 'home', component: homeView }],
      // 直接将【系统管理】模块里的子节点加载到这里(...将数组展开)
      ...xitongguanli_routes.children,
      ...fangchanguanli_routes.children,
      ...zhanghaoguanli_routes.children,
      ...feiyongguanli_routes.children,
      ...weixin_routes.children,
      ...yezhuguanli_routes.children,
      ...notice_routes.children,
      ...report_routes.children,
      ...qrcode_routes.children,
      // // 未找到页面
      // { path: '/:pathMatch(.*)*', name: 'NotFound', component: NotFoundView }
    ]
  },
  // 当然也可以加载到父节点（这时要求xitongguanli.js中/xitongguanli页面的组件为layoutView）
  // xitongguanli_routes,
]

const router = new VueRouter({
  mode: 'history',
  base: process.env.BASE_URL,
  routes
})

// 路由全局前置守卫，当会话存储中不存在token时都跳转到登录页
// geocoder腾讯地图请求地址api中（本地代理请求）
router.beforeEach((to, from, next)=>{
  if (to.name !== 'login' && !sessionStorage.token && !to.path.includes('geocoder')) next({ name: 'login' })
  else next()
})

export default router
