<template>
  <v-row>
    <v-col cols="12" sm="12" md="12" style="height:100%">
      <v-card height="100%" style="overflow: auto;">
        <v-snackbar app top timeout="2000" :color="snackbar.color" v-model="snackbar.show">{{ snackbar.text }}
        </v-snackbar>
        <v-card-title>系统设置</v-card-title>
        <v-card-text>
          <v-text-field v-model="form.sitename" label="系统名称"></v-text-field>
          <v-switch v-model="form.sitestatus" :label="`系统状态: ${form.sitestatus == true ? '开启' : '关闭'}`"></v-switch>
        </v-card-text>

        <v-card-title>微信第三方平台</v-card-title>
        <v-card-text>
          <v-text-field v-model="form.wechat.appid" label="app_id"></v-text-field>
          <v-text-field v-model="form.wechat.appsecret" label="app_secret"></v-text-field>
          <v-text-field v-model="form.wechat.token" label="token"></v-text-field>
          <v-text-field v-model="form.wechat.encodingAESKey" label="encodingAESKey"></v-text-field>
          <span>验证票据：{{ form.wechat.component_verify_ticket }}</span><br>
          <span>系统_Access_Token:{{ form.wechat.component_access_token }}</span><br>
          <span>Token有效期(秒):{{ form.wechat.expires_in }}</span><br>
          <span>上次更新Token时间:{{ new Date(form.wechat.update_time) }}</span><br>
          <span>上次getTime:{{ new Date(form.wechat.update_time).getTime() / 1000 }}</span> |
          <span>本次getTime:{{ new Date().getTime() / 1000 }}</span> |
          <span class="red--text">Token有效时间剩余:{{ expires_time }}</span><br>
        </v-card-text>

        <v-card-title>微信支付服务商平台</v-card-title>
        <v-card-text>
          <v-text-field v-model="form.wepay_partner.appid" label="app_id"></v-text-field>
          <v-text-field v-model="form.wepay_partner.mchid" label="mchid"></v-text-field>
          <v-text-field v-model="form.wepay_partner.serial_no" label="serial_no"></v-text-field>
          <v-text-field v-model="form.wepay_partner.privateKey" label="privateKey"></v-text-field>
          <v-text-field v-model="form.wepay_partner.apiv3_private_key" label="apiv3_private_key"></v-text-field>
        </v-card-text>
        <v-card-actions>
          <v-btn color="primary" @click="submit">
            保 存
          </v-btn>
          <v-btn color="primary" @click="get_data()" class="ml-5">
            刷 新
          </v-btn>
        </v-card-actions>
      </v-card>
    </v-col>
  </v-row>
</template>

<script>
export default {
  data: () => {
    return {
      snackbar: {
        show: false,
        text: "",
        color: "success",
        site_error: ''
      },
      form: {
        sitename:'',
        wechat: {
          appid: '',
          appsecret: '',
          token: '',
          encodingAESKey: '',
          component_verify_ticket: '', // 验证票据
          // 令牌及过期时间
          component_access_token: '',
          expires_in: '',
          // 令牌更新时间
          update_time: ''
        },
        wepay_partner: {
          appid: '', // 服务商应用ID
          mchid: '', //微信支付服务商 - 商户号 mchid
          serial_no: '', // 微信支付服务商商户证书序列号，生成证书后可以看到证书序列号
          privateKey: '', // 微信支付服务商证书密钥pem格式，文件所在的位置
        },
        sitestatus:''
      },
    }
  },
  computed: {
    expires_time: function () {
      return this.form.wechat.expires_in - (new Date().getTime() - new Date(this.form.wechat.update_time).getTime()) / 1000
    }
  },
  methods: {
    get_data() {
      this.$axios.get("/config")
        .then((res) => {
          if (res.data) {
            this.form = res.data
          }
        })
        .catch(error => {
          this.snackbar = {
            show: true,
            text: error,
            color: "error"
          }
        })
    },
    post_data() {
      this.$axios.post("/config/update", this.form)
        .then((res) => {
          console.log(res.data)
          this.snackbar = {
            show: true,
            text: res.data.msg,
            color: "success"
          }
        })
        .catch(error => {
          this.snackbar = {
            show: true,
            text: error,
            color: "error"
          }
        })
    },
    submit() {
      this.post_data()
      this.get_data()
    }
  },
  mounted() {
    this.get_data()
  }
}
</script>