import yezhulistView from '../views/yezhuguanli/yezhulistView.vue'

import layoutView from '../views/layoutView.vue'

const yezhuguanli_routes = {
  path: '/yezhuguanli',
  component: layoutView,
  children: [
    {
      path: '/yezhuguanli/yezhulists',
      name: 'yezhulists',
      component: yezhulistView
    },
  ]
}

export default yezhuguanli_routes