<template>
    <v-row>
        <v-snackbar app top timeout="2000" :color="snackbar.color" v-model="snackbar.show">{{ snackbar.text }}
        </v-snackbar>
        <XiaoquLists @select_xiaoqu="select_xiaoqu"></XiaoquLists>
        <v-col cols="12" sm="9" md="9">
            <v-card min-height="100%">
                <v-card-title>{{ xiaoqu ? xiaoqu.name : '' }} 巡逻记录列表</v-card-title>
                <v-card-text style="flex:1;">
                    <v-data-table :headers="headers" :items="items" item-key="_id" :loading="loading"
                        :options.sync="options" :server-items-length="total_num">
                        <template v-slot:[`item.date`]="{ item }">
                            {{ new Date(item.date).toLocaleString() }}
                        </template>
                    </v-data-table>
                </v-card-text>
            </v-card>
        </v-col>
    </v-row>
</template>
<script>
import XiaoquLists from "@/views/components/xiaoqu-lists.vue";
export default {
    components: {
        XiaoquLists
    },
    data: () => ({
        snackbar: {
            show: false,
            text: "",
            color: "success",
        },
        loading: false,
        total_num: 0,
        options: {},
        items: [],
        xiaoqu: null,
        headers: [
            { text: "时间", sortable: true, value: "date" },
            { text: "点位", sortable: true, value: "qrcode_name" },
            { text: "姓名", sortable: false, value: "nickname" },
            { text: "内容", sortable: false, value: "content" },
        ],
    }),
    watch: {
        options: {
            handler() {
                if (this.xiaoqu) {
                    this.get_record_lists(this.xiaoqu._id)
                }
            },
            deep: true,
        },
    },
    methods: {

        // 子组件事件 选择小区
        select_xiaoqu(xiaoqu) {
            this.xiaoqu = xiaoqu
            if (xiaoqu) {
                this.get_record_lists(xiaoqu._id)
            } else {
                this.items = []
            }
        },

        // 获取巡逻巡检记录
        get_record_lists(xiaoqu_id) {
            this.loading = true
            this.$axios.get('/qrcode/record/get_sheet_lists', { params: { xiaoqu: xiaoqu_id, page: this.options.page, itemsPerPage: this.options.itemsPerPage } })
                .then((res) => {
                    res.data.doc.forEach(element => {
                        element.detail.forEach(element_detail => {
                            element[element_detail.name] = element_detail.value
                        })
                    });
                    setTimeout(() => {
                        this.loading = false
                        this.items = res.data.doc
                        this.total_num = res.data.total_num
                    }, 500);
                })
        },
    },
};
</script>
