<template>
  <v-list>
    <v-list-item>
      <v-list-item-icon>
        <v-icon color="primary">mdi-book-open-variant</v-icon>
      </v-list-item-icon>
      <v-list-item-content>
        <v-list-item-title>物业电子台账系统</v-list-item-title>
      </v-list-item-content>
    </v-list-item>
    <v-list-group v-for="item in items" :key="item._id" v-model="item.active" :prepend-icon="item.icon" no-action>
      <template v-slot:activator>
        <v-list-item-content>
          <v-list-item-title v-text="item.name"></v-list-item-title>
        </v-list-item-content>
      </template>
      <v-list-item v-for="child in item.children" :key="child.name" :to="child.link" @click="add_tab(child)">
        <!-- <v-list-item-icon>
          <v-icon v-text="child.icon"></v-icon>
        </v-list-item-icon> -->
        <v-list-item-content>
          <v-list-item-title v-text="child.name"></v-list-item-title>
        </v-list-item-content>
      </v-list-item>
    </v-list-group>
  </v-list>
</template>

<script>

export default {
  data: () => ({
    items: [
      {
        _id: "0",
        icon: "mdi-cog",
        name: "系统管理",
        link: "/xitongguanli",
        // 二级菜单
        children: [
          { _id: "1", icon: "mdi-cog", name: "系统设置", link: "/xitongguanli/xitongshezhi" },
          { _id: "2", icon: "mdi-cog", name: "模块管理", link: "/xitongguanli/mokuaiguanli" },
          { _id: "3", icon: "mdi-cog", name: "企业管理", link: "/xitongguanli/qiyeguanli" },
          { _id: "4", icon: "mdi-cog", name: "权限设置", link: "/xitongguanli/quanxianshezhi" },
          { _id: "5", icon: "mdi-cog", name: "用户管理", link: "/xitongguanli/yonghuguanli" },
          { _id: "6", icon: "mdi-cog", name: "合作申请", link: "/xitongguanli/join" }
        ],
      }
    ]
  }),
  methods: {
    add_tab(new_tab) {
      if (this.$store.state.tabs.indexOf(new_tab) == -1) {
        this.$store.state.tabs.push(new_tab)
        // 直接Pushi到store（如上）,或者采用执行变更模式（如下）
        // this.$store.commit("add",new_tab)
      }
    },
    // 获取菜单列表
    async getlist() {
      const user_doc = await this.$axios.get('user/getuserinfo')
      this.$axios.get('navigation').then((res) => {
        if (user_doc.data.username == 'admin') {
          this.items = [...res.data, ...this.items]
        } else {
          this.items = res.data
        }
      })
    }
  },
  created() {
    this.getlist()
  }
};
</script>

