import noticelistView from '../views/notice/listsView.vue'

import layoutView from '../views/layoutView.vue'

const notice_routes = {
  path: '/notice',
  component: layoutView,
  children: [
    {
      path: '/notice/lists',
      name: 'notice_lists',
      component: noticelistView
    },
  ]
}

export default notice_routes