<template>
  <v-row>
    <v-snackbar app top timeout="5000" :color="snackbar.color" v-model="snackbar.show">{{ snackbar.text }}
    </v-snackbar>
    <v-col>
      <v-card cols="12" md="12">
        <v-card-title>授权账号管理
          <v-spacer></v-spacer>
          <v-btn color="primary" @click="get_data" class="mr-5">刷新</v-btn>
          <v-btn color="primary" @click="auth" class="mr-5">新增授权</v-btn>
        </v-card-title>
        <v-card-text>

          <!-- 绑定/解绑弹出框 -->
          <v-dialog v-model="dialog" max-width="500px" persistent :retain-focus="false">
            <v-card>
              <v-card-title>
                <span class="text-h5">操作{{ edit_authorizer_appid }}</span>
              </v-card-title>
              <v-card-text>
                <v-text-field v-model="edit_appid" :label="dialog_field_label"></v-text-field>
              </v-card-text>
              <v-card-actions>
                <v-spacer></v-spacer>
                <v-btn color="blue darken-1" text @click="close">
                  取消
                </v-btn>
                <v-btn color="blue darken-1" text @click="save(edit_authorizer_appid, edit_appid)">
                  {{ btn_bind_unbind_save_title }}
                </v-btn>
              </v-card-actions>
            </v-card>
          </v-dialog>

          <!-- 授权账号表格 -->
          <v-data-table :headers="headers" :items="items" item-key="_id" show-expand single-expand>
            <!-- 类型 -->
            <template v-slot:[`item.type`]="{ item }">
              <span v-if="item.authorizer_info.MiniProgramInfo">小程序</span>
              <span v-else>公众号</span>
            </template>
            <!-- 名称 -->
            <template v-slot:[`item.authorizer_info.nick_name`]="{ item }">
              <span>{{item.authorizer_info.nick_name}}_</span>
              <span v-if="item.authorizer_info.MiniProgramInfo">小程序</span>
              <span v-else>公众号</span>
            </template>
            <!-- logo -->
            <template v-slot:[`item.authorizer_info.head_img`]="{ item }">
              <v-img :src="item.authorizer_info.head_img" max-width="50"></v-img>
            </template>
            <!-- 开放平台操作 -->
            <template v-slot:[`item.open_appid_action`]="{ item }">
              <v-btn small text @click="create_open_app(item.authorizer_appid)" color="success">创建</v-btn>
              <v-btn small text @click="get_open_app(item.authorizer_appid)" color="blue-grey">获取</v-btn>
              <div></div>
              <v-btn small text @click="bind_dialog(item.authorizer_appid)" color="primary">绑定</v-btn>
              <v-btn small text @click="unbind_dialog(item.authorizer_appid)" color="error">解绑</v-btn>
            </template>
            <!-- 小程序代码操作 -->
            <template v-slot:[`item.wxapp_code_action`]="{ item }">
              <template v-if="item.authorizer_info.MiniProgramInfo">

                <v-btn small text @click="commit(item.authorizer_appid)" color="success">上传</v-btn>
                <v-btn small text @click="getTrialQRCode(item.authorizer_appid)" color="blue-grey">体验</v-btn>
                <div></div>
                <v-btn small text @click="setPrivacySetting(item.authorizer_appid)" color="primary">设置隐私指引</v-btn>
                <div></div>
                <v-btn small text @click="submit_audit(item.authorizer_appid)" color="error">提审</v-btn>
                <v-btn small text @click="getLatestAuditStatus(item.authorizer_appid)" color="blue-grey">查询</v-btn>
                <v-btn small text @click="release(item.authorizer_appid)" color="primary">发布</v-btn>

              </template>
            </template>
            <!-- 关联小程序操作 -->
            <template v-slot:[`item.linkminiprogram`]="{ item }">
              <!-- <v-btn small text v-if="!item.authorizer_info.MiniProgramInfo"
                @click="getLinkMiniprogram(item.authorizer_appid)" color="blue-grey">创建</v-btn> -->
              <v-btn small text v-if="!item.authorizer_info.MiniProgramInfo"
                @click="linkMiniprogram(item.authorizer_appid)" color="primary">关联</v-btn>
              <div>{{ item.linkminiprogram }}</div>
            </template>
            <!-- 设置公众号菜单 -->
            <template v-slot:[`item.set_menu`]="{ item }">
              <v-btn v-if="!item.authorizer_info.MiniProgramInfo" @click="set_menu(item.authorizer_appid)" small
                color="primary">设置
              </v-btn>
            </template>
            <!-- 详情 -->
            <template v-slot:expanded-item="{ headers, item }">
              <td :colspan="headers.length">
                <v-row>
                  <!-- 左侧 -->
                  <v-col cols="12" sm="6">
                    <p class="caption">【调用令牌】{{ item.authorizer_access_token }}</p>
                    <p class="caption">【调用令牌有效期】{{ item.expires_in }} | 剩余有效期：{{ item.expires_in - (new Date().getTime()
                    -
                    new
                    Date(item.update_time).getTime()) / 1000
                    }}</p>
                    <p class="caption">【刷新令牌】{{ item.authorizer_refresh_token }}</p>
                    <v-btn v-if="item.authorizer_info.MiniProgramInfo" color="primary" small text @click="modifyServerDomain(item.authorizer_appid)">配置小程序服务器域名
                    </v-btn>
                    <v-btn color="primary" small text @click="get_authorizer_info(item.authorizer_appid)">更新授权账号详情
                    </v-btn>
                    <p class="caption">【公众号/小程序类型】{{ item.authorizer_info.service_type_info }}</p>
                    <p class="caption">【公众号/小程序认证类型】{{ item.authorizer_info.verify_type_info }}</p>
                    <p class="caption">【主体名称】{{ item.authorizer_info.principal_name }}</p>
                    <p class="caption">【帐号介绍】{{ item.authorizer_info.signature }}</p>
                    <p class="caption" v-if="item.authorizer_info.MiniProgramInfo">
                      【小程序配置信息】{{ item.authorizer_info.MiniProgramInfo }}</p>
                    <p class="caption">【帐号状态】{{ item.authorizer_info.account_status }}</p>
                    <v-img width="250" style="margin:0 auto" :src="'https://api.dayangtong.cn:8080/' + item.qr_code_img">
                    </v-img>
                    <v-file-input placeholder="上传二维码" @change="post_qr_code(item)" v-model="item.qr_code_img">
                    </v-file-input>
                  </v-col>
                  <!-- 右侧-公众号配置 -->
                  <v-col cols="12" sm="6" v-if="!item.authorizer_info.MiniProgramInfo">
                    <p>【模板消息】</p>
                    <v-btn color="primary" small text @click="get_all_private_template(item.authorizer_appid)">获取模板列表
                    </v-btn>
                    <div v-if="item.template">
                      <div v-if="item.template.template_list">
                        <div v-for="template in item.template.template_list" :key="template.template_id"
                          style="border-bottom: solid;">
                          <p>{{ template.title }}</p>
                          <p>{{ template.template_id }}</p>
                          <p>{{ template.content }}</p>
                          <p>{{ template.example }}</p>
                        </div>
                      </div>
                      <div v-else>{{ item.template }}</div>
                    </div>
                  </v-col>
                  <!-- 右侧-小程序配置 -->
                  <v-col v-else>
                    <p>【扫普通链接二维码打开小程序】</p>
                    <p>二维码规则：https://api.dayangtong.cn:8080/{{ item.authorizer_appid }}</p>
                    <p>小程序功能页面：pages/qrcode/index</p>
                  </v-col>
                </v-row>
              </td>
            </template>
          </v-data-table>
        </v-card-text>
      </v-card>
    </v-col>
  </v-row>
</template>

<script>
export default {
  data: () => {
    return {
      snackbar: {
        show: false,
        text: "",
        color: "success",
      },
      headers: [
        { text: 'open_appid', value: 'open_appid' },
        { text: 'appid', value: 'authorizer_appid' },
        { text: '头像', value: 'authorizer_info.head_img' },
        { text: '昵称', value: 'authorizer_info.nick_name' },
        { text: '授权状态', value: 'status' },
        { text: '开放平台', value: 'open_appid_action', align: 'center' },
        { text: '代码操作', value: 'wxapp_code_action', align: 'center' },
        { text: '关联小程序', value: 'linkminiprogram', align: 'center' },
        { text: '菜单', value: 'set_menu' },
        { text: '', value: 'data-table-expand' },
      ],
      dialog: false,
      btn_bind_unbind_save_title: '', // 绑定/解绑
      edit_appid: '',
      dialog_field_label: '',
      edit_authorizer_appid: '',
      items: [],
    }
  },
  methods: {
    get_data() {
      this.$axios.get("/weixin/authorizedaccount")
        .then((res) => {
          if (res.data) {
            this.items = res.data
          }
        })
    },
    auth() {
      this.$axios.post("/weixin/get_auth_url")
        .then((res) => {
          console.log(res)
          window.open("https://api.dayangtong.cn:8080/weixin/auth?auth_url=" + encodeURIComponent(res.data), '_blank')
        })
    },

    // 设置公众号的标准菜单
    set_menu(authorizer_appid) {
      this.dialog = true
      this.btn_bind_unbind_save_title = '创建菜单'
      this.dialog_field_label = '菜单中展示的小程序的appid'
      this.edit_authorizer_appid = authorizer_appid
    },

    // 创建开放平台
    create_open_app(authorizer_appid) {
      this.$axios.post('/weixin/mp/open/create', { authorizer_appid: authorizer_appid })
        .then((res) => {
          console.log(res)
          if (res.data.errcode == 0) {
            this.get_data()
          }
          this.snackbar = {
            show: true,
            text: res.data.errmsg,
          }
        })
    },

    // 获取绑定的开放平台
    get_open_app(authorizer_appid) {
      this.$axios.post('/weixin/mp/open/get', { authorizer_appid: authorizer_appid })
        .then((res) => {
          console.log(res)
          if (res.data.errcode == 0) {
            this.get_data()
          }
          this.snackbar = {
            show: true,
            text: res.data.errmsg,
          }
        })
    },

    // 打开绑定弹窗
    bind_dialog(authorizer_appid) {
      this.dialog = true
      this.btn_bind_unbind_save_title = '绑定'
      this.dialog_field_label = '绑定的开放平台的appid'
      this.edit_authorizer_appid = authorizer_appid
    },

    // 打开解绑弹窗
    unbind_dialog(authorizer_appid) {
      this.dialog = true
      this.btn_bind_unbind_save_title = '解绑'
      this.dialog_field_label = '解绑的开放平台的appid'
      this.edit_authorizer_appid = authorizer_appid
    },

    // 弹窗-取消
    close() {
      this.dialog = false,
        this.edit_appid = ''
      this.dialog_field_label = ''
      this.edit_authorizer_appid = ''
      this.btn_bind_unbind_save_title = ''
    },

    // 弹窗-保存
    save(edit_authorizer_appid, edit_appid) {
      if (this.btn_bind_unbind_save_title == '绑定') {
        this.$axios.post('/weixin/mp/open/bind', { authorizer_appid: edit_authorizer_appid, open_appid: edit_appid })
          .then((res) => {
            console.log(res)
            if (res.data.errcode == 0) {
              this.get_data()
            }
            this.close()
            this.snackbar = {
              show: true,
              text: res.data.errmsg,
            }
          })
      } else if (this.btn_bind_unbind_save_title == '解绑') {
        this.$axios.post('/weixin/mp/open/unbind', { authorizer_appid: edit_authorizer_appid, open_appid: edit_appid })
          .then((res) => {
            console.log(res)
            if (res.data.errcode == 0) {
              this.get_data()
            }
            this.dialog_unbind = false
            this.snackbar = {
              show: true,
              text: res.data.errmsg,
            }
          })
      } else if (this.btn_bind_unbind_save_title == '关联') {
        this.$axios.post('/weixin/mp/open/linkminiprogram', { authorizer_appid: edit_authorizer_appid, miniprogram_appid: edit_appid })
          .then((res) => {
            console.log(res)
            this.get_data()
            this.snackbar = {
              show: true,
              text: res.data.errmsg
            }
          })
      }
      else if (this.btn_bind_unbind_save_title == '创建菜单') {
        this.$axios.post('/weixin/mp/menu/create', { authorizer_appid: edit_authorizer_appid, miniprogram_appid: edit_appid })
          .then((res) => {
            console.log(res)
            this.snackbar = {
              show: true,
              text: res.data.errmsg
            }
          })
      }
      else if (this.btn_bind_unbind_save_title == '上传代码并生成体验版') {
        this.$axios.post('/weixin/wxapp/commit', { authorizer_appid: edit_authorizer_appid, template_id: edit_appid })
          .then(res => {
            console.log(res.data)
            this.snackbar = {
              show: true,
              text: res.data.msg
            }
          })
      }
      this.close()
    },

    // 上传代码并生成体验版
    commit(authorizer_appid) {
      this.dialog = true
      this.btn_bind_unbind_save_title = '上传代码并生成体验版'
      this.dialog_field_label = '代码库中的代码模板 ID'
      this.edit_authorizer_appid = authorizer_appid
    },

    // 下载体验版本
    getTrialQRCode(authorizer_appid) {
      let a = document.createElement('a')
      a.setAttribute('download', 'qrcode')
      a.href = 'https://api.dayangtong.cn:8080/weixin/wxapp/getTrialQRCode?authorizer_appid=' + authorizer_appid
      a.click()
    },

    // 设置小程序用户隐私保护指引
    setPrivacySetting(authorizer_appid) {
      this.$axios.post('/weixin/wxapp/setPrivacySetting', { authorizer_appid: authorizer_appid })
        .then(res => {
          console.log(res.data)
          this.snackbar = {
            show: true,
            text: res.data.errmsg
          }
        })
    },

    // 提交小程序代码审核
    submit_audit(authorizer_appid) {
      this.$axios.post('/weixin/wxapp/submit_audit', { authorizer_appid: authorizer_appid })
        .then(res => {
          console.log(res.data)
          this.snackbar = {
            show: true,
            text: res.data.errmsg
          }
        })
    },

    // 查询小程序代码审核状态
    getLatestAuditStatus(authorizer_appid) {
      this.$axios.post('/weixin/wxapp/getLatestAuditStatus', { authorizer_appid: authorizer_appid })
        .then(res => {
          console.log(res.data)
          this.snackbar = {
            show: true,
            text: res.data.msg
          }
        })
    },

    // 发布已通过审核的小程序
    release(authorizer_appid) {
      this.$axios.post('/weixin/wxapp/release', { authorizer_appid: authorizer_appid })
        .then(res => {
          console.log(res.data)
          this.snackbar = {
            show: true,
            text: res.data.msg
          }
        })
    },

    // 公众号关联小程序
    linkMiniprogram(authorizer_appid) {
      this.dialog = true
      this.btn_bind_unbind_save_title = '关联'
      this.dialog_field_label = '关联的小程序的appid'
      this.edit_authorizer_appid = authorizer_appid
    },

    // 配置小程序服务器域名
    modifyServerDomain(authorizer_appid) {
      this.$axios.post("/weixin/mp/modifyServerDomain", { authorizer_appid: authorizer_appid })
        .then(res => {
          console.log(res.data)
          this.snackbar = {
            show: true,
            text: res.data.errmsg
          }
        })
    },

    // 获取授权账号详情
    get_authorizer_info(authorizer_appid) {
      this.$axios.post("/weixin/mp/get_authorizer_info", { authorizer_appid: authorizer_appid })
        .then(res => {
          console.log(res.data)
          this.get_data()
          this.snackbar = {
            show: true,
            text: "更新成功",
            color: "success",
          }
        })
    },

    // 获取公众号模板消息列表
    get_all_private_template(authorizer_appid) {
      this.$axios.post('/weixin/mp/open/get_all_private_template', { authorizer_appid: authorizer_appid })
        .then(res => {
          console.log(res)
          this.get_data()
        })
    },

    // 上传小程序/公众号二维码
    post_qr_code(item) {
      const formData = new FormData()
      formData.append('qr_code_img', item.qr_code_img)
      formData.append('authorizer_appid', item.authorizer_appid)
      this.$axios.post('/weixin/mp/open/qr_code_img', formData)
        .then(res => {
          item.qr_code_img = res.data.qr_code_img
        })
    },
  },

  mounted() {
    this.get_data()
  }
}
</script>

<style scoped>
.v-data-table__expanded p {
  margin: 5px 0;
}
</style>