<template>
    <v-row>
        <v-snackbar app top timeout="2000" :color="snackbar.color" v-model="snackbar.show">{{ snackbar.text }}
        </v-snackbar>
        <v-col cols="12" sm="12" md="12" style="height:100%">
            <v-card height="100%" style="overflow: auto;">
                <v-card-title>订单列表
                    <v-spacer></v-spacer>
                    <v-btn @click="refresh" color="primary">
                        刷新
                    </v-btn>
                </v-card-title>
                <v-card-text>
                    <v-dialog v-model="dialog" max-width="280px" persistent>
                        <v-card>
                            <v-card-title>
                                <span>退款</span>
                            </v-card-title>

                            <v-card-text>
                                <p class="red--text">退款后无法恢复，确定退款么？</p>
                            </v-card-text>

                            <v-card-actions>
                                <v-spacer></v-spacer>
                                <v-btn color="blue darken-1" text @click="close" :loading="loading">
                                    取消
                                </v-btn>
                                <v-btn color="blue darken-1" text @click="refunds(refunds_item)" :loading="loading">
                                    确定退款
                                </v-btn>
                            </v-card-actions>
                        </v-card>
                    </v-dialog>
                    <!-- 表格 -->
                    <v-data-table :headers="headers" :items="order_items" item-key="_id" :loading="table_loading"
                        :options.sync="options" :server-items-length="total_num" show-expand height="66vh">
                        <template v-slot:[`item.actions`]="{ item }">
                            <v-btn v-if="item.status == 'SUCCESS'" small text color="red" @click="open_dialog(item)">退款
                            </v-btn>
                        </template>
                        <template v-slot:[`item.status`]="{ item }">
                            <span v-if="item.status=='SUCCESS'">支付成功</span>
                            <span v-else-if="item.status=='PROCESSING'">退款处理中</span>
                            <span v-else-if="item.status=='REFUND'">已退款</span>
                            <span v-else>{{item.status}}</span>
                        </template>
                        <template v-slot:[`item.date`]="{ item }">
                            {{ new Date(item.date).toLocaleString() }}
                        </template>
                        <template v-slot:expanded-item="{ headers, item }">
                            <td :colspan="headers.length">
                                <p v-if="item.status == 'REFUND'">
                                    已退款：
                                    【退款人：{{item.refund_user.nickname}}
                                    】【退款时间：{{new Date(item.refund_date).toLocaleString()}}
                                    】【退款到账：{{item.user_received_account}}】
                                </p>
                                <p v-for="(detail_item,index) of item.detail" :key="index">
                                    第{{index+1}}条：
                                    【名称：{{detail_item.mingcheng}}
                                    】【开始：{{detail_item.start}}
                                    】【结束：{{detail_item.end}}
                                    】【周期/倍数：{{detail_item.zhouqi_beishu}}
                                    】【数量：{{detail_item.shuliang}}
                                    】【单价：{{detail_item.danjia}}
                                    】【总计：{{detail_item.zongji}}】
                                </p>
                            </td>
                        </template>
                    </v-data-table>
                </v-card-text>
            </v-card>
        </v-col>
    </v-row>
</template>
<script>
export default {
    data: () => ({
        snackbar: {
            show: false,
            text: "",
            color: "success",
        },
        refunds_item:null,
        options: {},
        total_num: 0,
        loading: false,
        table_loading: false,
        dialog:false,
        // 费项列表
        order_items: [],
        headers: [
            { text: '', value: 'data-table-expand' },
            { text: "订单号", sortable: false, value: "_id" },
            { text: "时间", sortable: false, value: "date" },
            { text: "小程序appid", sortable: false, value: "appid" },
            { text: "用户openid", sortable: false, value: "openid" },
            { text: "商户号mchid", sortable: false, value: "mchid" },
            { text: "金额", sortable: false, value: "total" },
            { text: "类型", sortable: false, value: "type" },
            { text: "状态", sortable: false, value: "status" },
            { text: "操作", sortable: false, value: "actions" },
        ],
    }),
    watch: {
        options: {
            handler() {
                this.get_order_lists()
            },
            deep: true,
        },
    },
    methods: {
        // 获取订单列表
        get_order_lists() {
            this.table_loading = true
            this.$axios.get("/wechat/order/lists", { params: { page: this.options.page, itemsPerPage: this.options.itemsPerPage } })
                .then((res) => {
                    setTimeout(() => {
                        this.table_loading = false
                        this.order_items = res.data.doc
                        this.total_num = res.data.total_num
                    }, 500);
                })
        },
        refresh(){
            this.get_order_lists()
        },
        close(){
            this.refunds_item = null,
            this.dialog = false
        },
        open_dialog(item){
            this.refunds_item = item,
            this.dialog = true
        },
        refunds(refunds_item){
            this.loading = true
            this.$axios.post("/wechat/pay/order/refunds",refunds_item)
                .then((res) => {
                    console.log(res)
                    if(res.data.message){
                        this.snackbar={
                            show: true,
                            text: res.data.message,
                            color: "error",
                        }
                    }else{
                        this.snackbar={
                            show: true,
                            text: res.data.status,
                            color: "success",
                        }
                    }
                    this.get_order_lists()
                    this.close()
                    this.loading = false
                })
        }
    }
}
</script>
