import zhanghaoliebiaoView from '../views/zhanghaoguanli/zhanghaoliebiaoView.vue'
import logView from '../views/zhanghaoguanli/logView.vue'
import quanxianguanliView from '../views/zhanghaoguanli/quanxianguanliView.vue'
import layoutView from '../views/layoutView.vue'

const zhanghaoguanli_routes = {
  path: '/zhanghaoguanli',
  component: layoutView,
  children: [
    {
      path: '/zhanghaoguanli/zhanghaoliebiao',
      name: 'zhanghaoliebiao',
      component: zhanghaoliebiaoView
    },
    {
      path: '/zhanghaoguanli/logs',
      name: 'log',
      component: logView
    },
    {
      path: '/zhanghaoguanli/quanxianguanli',
      name: 'quanxianguanli',
      component: quanxianguanliView
    },
  ]
}

export default zhanghaoguanli_routes