<template>
    <v-card>
        <v-card-title class="text-button font-weight-black">
            <v-progress-circular indeterminate color="primary" v-show="loading">
            </v-progress-circular>
            收款单：{{ shoukuandan_id }}
            <v-spacer></v-spacer>
            <template v-if="skd_info.status == 1">
                <div class="red--text">已撤单：{{ skd_info.cancel_remark }}</div>
            </template>
            <template v-else-if="skd_info.status == 0">
                <v-form v-model="valid">
                    <v-text-field placeholder="撤单说明" v-model="cancel_remark" dense hide-details="auto" color="error"
                        :rules="[v => !!v]"></v-text-field>
                </v-form>
                <v-btn color="error" class="ml-5" small :disabled="!valid" @click.once="btn_cancel">
                    撤 单
                </v-btn>
            </template>
        </v-card-title>
        <v-card-text>
            <!-- lodop错误提示信息 -->
            <div ref="lodop_msg"></div>
            <div style="border:1px solid red;" ref="print">
                <!-- 打印区域开始 -->
                <div class="pa-4" style="padding:16px;color:#222;">
                    <!-- header -->
                    <div style="text-align:center;font-size: 20px;font-weight: bold;line-height: 1.5;">
                        {{ skd_info.xiaoqu.name }}收费凭证【{{ skd_info.company.qiyejiancheng }}】</div>
                    <v-divider style="margin:4px 0"></v-divider>
                    <div style="display:flex;justify-content: space-between;font-weight: bold;margin: 10px 0;">
                        <span>房号：{{ skd_info.loudong.name + skd_info.danyuan.name + skd_info.fangwu.name }}</span>
                        <span :style="skd_info.status == 1 ? 'text-transform: uppercase;text-decoration: line-through' : 'text-transform: uppercase;'">凭证：{{
                        skd_info._id
                        }}</span>
                        <span>业主姓名：{{ skd_info.fangwu.yezhu }}</span>
                    </div>
                    <!-- content -->
                    <table width="100%" cellspacing="1" style="background-color: black;text-align: center;color:#666">
                        <tr>
                            <th style="background-color: #ffffff;padding:5px 0">项目</th>
                            <th style="background-color: #ffffff;">单价</th>
                            <th style="background-color: #ffffff;">起数</th>
                            <th style="background-color: #ffffff;">止数</th>
                            <th style="background-color: #ffffff;">周期/倍数</th>
                            <th style="background-color: #ffffff;">数量</th>
                            <th style="background-color: #ffffff;">总计</th>
                        </tr>
                        <tr v-for="item in skd_info.feiyong" :key="item._id">
                            <td style="background-color: #ffffff;padding:5px 0">{{ item.mingcheng }}</td>
                            <td style="background-color: #ffffff;">{{ item.danjia }}</td>
                            <td style="background-color: #ffffff;">{{ item.start }}</td>
                            <td style="background-color: #ffffff;">{{ item.end }}</td>
                            <td style="background-color: #ffffff;">{{ item.zhouqi_beishu }}</td>
                            <td style="background-color: #ffffff;">{{ item.shuliang }}</td>
                            <td style="background-color: #ffffff;">{{ item.zongji }}</td>
                        </tr>
                    </table>
                    <!-- footer -->
                    <div>
                        <div style="display:flex;justify-content: space-between;font-weight: bold;margin: 10px 0;">
                            <span>应收：￥{{ yingshou }}{{ youhui != 0 ? ' - 优惠：￥' +
                            skd_info.youhui : ''
                            }} =
                                实收：￥{{ shishou }}</span>
                            <span>实收（大写）：{{ convertCurrency(shishou) }}</span>
                        </div>
                        <div style="display:flex;justify-content: space-between;font-weight: bold;margin: 4px 0;">
                            <span>备注：{{ skd_info.remark }}</span>
                            <span class="float-right">收费员：{{ skd_info.user?skd_info.user.nickname:'无' }} @
                                收费方式：{{ skd_info.pay_way }} @ {{ skd_info.date_china }}</span>
                        </div>
                    </div>
                </div>
                <!-- 打印区域结束 -->
            </div>
        </v-card-text>
        <v-card-actions>
            <v-spacer></v-spacer>
            <v-btn text @click="$emit('close')">
                关 闭
            </v-btn>
            <v-btn @click="print" text>
                打 印
            </v-btn>
        </v-card-actions>
    </v-card>
</template>
<script>
import { getLodop } from '@/utils/LodopFuncs' //导入模块
export default {
    data: () => ({
        skd_info: {
            company:{},
            xiaoqu: {},
            loudong: {},
            danyuan: {},
            fangwu: {},
            user: {},
        },
        loading: false,
        yingshou: 0.00,
        youhui: 0.00,
        shishou: 0.00,
        cancel_remark: '',
        valid: false
    }),
    props: {
        shoukuandan_id: String
    },
    watch: {
        shoukuandan_id(val) {
            this.skd_info = {
                company:{},
                xiaoqu: {},
                loudong: {},
                danyuan: {},
                fangwu: {},
                user: {},
            }
            this.yingshou = 0.00
            this.youhui = 0.00
            this.shishou = 0.00
            this.cancel_remark = ''
            // 获取收款单详细信息
            if (val != '') {
                this.get_shoukuandan(val)
            }
        }
    },
    created() {
        // 首次挂载时先加载一次信息
        this.get_shoukuandan(this.shoukuandan_id)
    },
    methods: {
        get_shoukuandan(_id) {
            this.loading = true
            this.$axios.get('shoukuandan', { params: { _id: _id } })
                .then((res) => {
                    setTimeout(() => {
                        this.skd_info = res.data
                        this.yingshou = Number(res.data.total).toFixed(2)
                        this.youhui = Number(res.data.youhui).toFixed(2)
                        this.shishou = (this.yingshou - this.youhui).toFixed(2)
                        this.loading = false
                    }, 500);
                })
        },

        // 撤销
        btn_cancel() {

            this.$axios.post('/shoukuandan/cancel', { shoukuandan: this.skd_info, remark: this.cancel_remark })
                .then((res) => {
                    if (res.data.message == '撤销成功') {
                        this.get_shoukuandan(this.shoukuandan_id)
                    }
                })
        },

        // 打印
        print() {
            let LODOP = getLodop()//调用getLodop获取LODOP对象
            if (LODOP.error) {
                this.$refs.lodop_msg.innerHTML = LODOP.msg
            } else {
                LODOP.PRINT_INIT("")
                LODOP.SET_PRINT_MODE("PRINT_PAGE_PERCENT", "Auto-Width");
                
                LODOP.SET_PRINT_PAGESIZE(1, 2100, 930, "");
                LODOP.SET_SHOW_MODE("PREVIEW_NO_MINIMIZE", true); //预览窗口禁止最小化，并始终最前	
                LODOP.SET_PRINT_MODE("AUTO_CLOSE_PREWINDOW", 1); //打印后自动关闭预览窗口
                var strFormHtml = this.$refs.print.innerHTML;
                LODOP.ADD_PRINT_HTM(0, 10, "100%", "100%", strFormHtml);
                LODOP.PREVIEW();
            }
        },

        // 转换大小写
        convertCurrency(n) {
            var fraction = ['角', '分'];
            var digit = [
                '零', '壹', '贰', '叁', '肆',
                '伍', '陆', '柒', '捌', '玖'
            ];
            var unit = [
                ['元', '万', '亿'],
                ['', '拾', '佰', '仟']
            ];
            var head = n < 0 ? '欠' : '';
            n = Math.abs(n);
            var s = '';
            for (let i = 0; i < fraction.length; i++) {
                s += (digit[Math.floor(n * 10 * Math.pow(10, i)) % 10] + fraction[i]).replace(/零./, '');
            }
            s = s || '整';
            n = Math.floor(n);
            for (let i = 0; i < unit[0].length && n > 0; i++) {
                var p = '';
                for (let j = 0; j < unit[1].length && n > 0; j++) {
                    p = digit[n % 10] + unit[1][j] + p;
                    n = Math.floor(n / 10);
                }
                s = p.replace(/(零.)*零$/, '').replace(/^$/, '零') + unit[0][i] + s;
            }
            return head + s.replace(/(零.)*零元/, '元')
                .replace(/(零.)+/g, '零')
                .replace(/^整$/, '零元整');
        }
    }
}
</script>