import shouqufeiyongView from '../views/feiyongguanli/shouqufeiyongView.vue'
import feixiangshezhiView from '../views/feiyongguanli/feixiangshezhiView.vue'
import taizhangtongjiView from '../views/feiyongguanli/taizhangtongjiView.vue'
import shoukuandanView from '../views/feiyongguanli/shoukuandanView.vue'

import layoutView from '../views/layoutView.vue'

const feiyongguanli_routes = {
  path: '/feiyongguanli',
  component: layoutView,
  children: [
    {
      path: '/feiyongguanli/shouqufeiyong',
      name: 'shouqufeiyong',
      component: shouqufeiyongView
    },
    {
      path: '/feiyongguanli/feixiangshezhi',
      name: 'feixiangshezhi',
      component: feixiangshezhiView
    },
    {
      path: '/feiyongguanli/taizhangtongji',
      name: 'taizhangtongji',
      component: taizhangtongjiView
    },
    {
      path: '/feiyongguanli/shoukuandan',
      name: 'shoukuandan',
      component: shoukuandanView
    },
  ]
}

export default feiyongguanli_routes