<template>
    <v-data-table :headers="headers" :items="items" class="elevation-2" style="flex:1" :loading="loading">
        <template v-slot:top>
            <v-snackbar app top timeout="2000" :color="snackbar.color" v-model="snackbar.show">{{ snackbar.text }}
            </v-snackbar>
            <v-toolbar flat>
                <v-toolbar-title>小区列表</v-toolbar-title>
                <v-spacer></v-spacer>
                <v-dialog v-model="dialog" max-width="500px">
                    <template v-slot:activator="{ on, attrs }">
                        <v-btn color="primary" class="mb-2" v-bind="attrs" v-on="on">
                            新增
                        </v-btn>
                    </template>
                    <v-card>
                        <v-card-title>
                            <span class="text-h5">{{ formTitle }}</span>
                        </v-card-title>

                        <v-card-text>
                            <v-container>
                                <v-row>
                                    <v-col cols="12" sm="6" md="6">
                                        <v-select :items="company_items" item-text="qiyejiancheng" item-value="_id"
                                            v-model="editedItem.company" label="物业公司"></v-select>
                                    </v-col>
                                    <v-col cols="12" sm="6" md="6">
                                        <v-text-field v-model="editedItem.name" label="小区名称"></v-text-field>
                                    </v-col>
                                    <v-col cols="12" sm="6" md="6">
                                        <v-text-field v-model="editedItem.phone" label="小区电话"></v-text-field>
                                    </v-col>
                                    <v-col cols="12" sm="6" md="6">
                                        <v-text-field v-model="editedItem.latlng" label="经纬度" :disabled="true">
                                        </v-text-field>
                                    </v-col>
                                    <v-col cols="12" sm="12" md="12">
                                        <v-text-field v-model="editedItem.address" label="详细地址">
                                            <template v-slot:append>
                                                <v-btn icon color="primary" @click="get_latlng(editedItem.address)">
                                                    <v-icon>mdi-magnify</v-icon>
                                                </v-btn>
                                            </template>
                                        </v-text-field>
                                        <div ref="map_container" style="height:200px"></div>
                                    </v-col>
                                </v-row>
                            </v-container>
                        </v-card-text>

                        <v-card-actions>
                            <v-spacer></v-spacer>
                            <v-btn color="blue darken-1" text @click="close">
                                取消
                            </v-btn>
                            <v-btn color="blue darken-1" text @click="save">
                                保存
                            </v-btn>
                        </v-card-actions>
                    </v-card>
                </v-dialog>
            </v-toolbar>
        </template>
        <template v-slot:[`item.actions`]="{ item }">
            <v-icon color="primary" small class="mr-2" @click="editItem(item)">
                mdi-pencil
            </v-icon>
            <v-icon color="error" small class="mr-2" @click="deleteItem(item)">
                mdi-delete
            </v-icon>
        </template>
    </v-data-table>
</template>
<script>
export default {
    data: () => ({
        snackbar: {
            show: false,
            text: "",
            color: "success",
            site_error: ''
        },
        loading: true,
        dialog: false,
        date_picker_dialog: false,
        company_items: [],
        headers: [
            { text: 'ID', value: '_id', },
            { text: '物业公司', value: 'company.qiyejiancheng', },
            { text: '小区名称', value: 'name' },
            { text: '详细地址', value: 'address' },
            { text: '经纬度', value: 'latlng' },
            { text: '小区电话', value: 'phone' },
            { text: '编辑/删除', align: 'center', value: 'actions' },
        ],
        items: [],
        editedIndex: -1,
        editedItem: {
            company: '',
            name: '',
            address: '',
            latlng: '39.90871879886639, 116.3973701092209',
            phone: ''
        },
        defaultItem: {
            company: '',
            name: '',
            address: '',
            latlng: '39.90871879886639, 116.3973701092209',
            phone: ''
        },
        map: null,
        marker: null
    }),

    computed: {
        formTitle() {
            return this.editedIndex === -1 ? '添加' : '编辑'
        },
    },

    watch: {
        dialog(val) {
            if (val) {
                this.$nextTick(() => {
                    this.initMap()
                })
            } else {
                this.close()
            }
        }
    },
    created() {
        this.getlist()
        // 获取企业列表
        this.$axios.get('/company/getlist')
            .then((res) => {
                this.company_items = res.data
            })
    },

    methods: {
        getlist() {
            this.items = []
            this.loading = true
            this.$axios.get('/xiaoqu/getlist')
                .then((res) => {
                    setTimeout(() => {
                        this.loading = false
                        this.items = res.data
                    }, 1000);
                })
        },

        editItem(item) {
            this.editedIndex = this.items.indexOf(item)
            this.editedItem = Object.assign({}, item)
            this.dialog = true
        },

        close() {
            this.dialog = false
            this.$nextTick(() => {
                this.editedItem = Object.assign({}, this.defaultItem)
                this.editedIndex = -1
            })
        },
        deleteItem(item) {
            this.$axios.post('/xiaoqu/del', item)
                .then((res) => {
                    console.log(res)
                    this.close()
                    this.getlist()
                    this.snackbar = {
                        show: true,
                        text: res.data.message,
                        color: res.data.error ? "error" : "success",
                    }
                })
                .catch(error => {
                    console.log(error)
                    this.snackbar = {
                        show: true,
                        text: error,
                        color: "error"
                    }
                })
        },
        initMap() {
            if (this.map) {
                this.map.destroy()
            }
            var latlng = this.editedItem.latlng?this.editedItem.latlng:'39.90871879886639, 116.3973701092209'
            var lat = latlng.split(",")[0]
            var lng = latlng.split(",")[1]
            setTimeout(() => {
                console.log(this.$refs.map_container)
                var center = new window.TMap.LatLng(lat, lng)
                this.map = new window.TMap.Map(this.$refs.map_container, {
                    center: center,//设置地图中心点坐标
                    zoom:3
                })
                this.setmarker()
                this.map.on("panend", this.panendHandler)
            }, 500);
        },

        // 拖动地图
        panendHandler(e) {
            console.log('拖动了')
            var key = "TGYBZ-ISKCF-MSGJM-NZ3ZF-U5QG2-LKF5I"
            var lat = e.target.getCenter().getLat()
            var lng = e.target.getCenter().getLng()
            this.$axios.get('http://localhost:8080/tx/ws/geocoder/v1/?key=' + key + '&location=' + lat + ',' + lng)
                .then((res) => {
                    this.editedItem.address = res.data.result.formatted_addresses.recommend
                })
            this.editedItem.latlng = lat + ',' + lng
            this.setmarker()
        },

        // 通过文字地址到经纬度的转换能力
        get_latlng(address) {
            var key = "TGYBZ-ISKCF-MSGJM-NZ3ZF-U5QG2-LKF5I"
            this.$axios.get('http://localhost:8080/tx/ws/geocoder/v1/?key=' + key + '&address=' + address)
                .then((res) => {
                    var lat = res.data.result.lat
                    var lng = res.data.result.lng
                    this.editedItem.latlng = lat + ',' + lng
                    this.map.setCenter(res.data.result.location)
                    this.setmarker()
                })
        },

        // 地图中心点标记
        setmarker() {
            if (this.marker) {
                this.marker.setMap(null);
                this.marker = null;
            }
            this.marker = new window.TMap.MultiMarker({
                map: this.map,
                styles: {
                    // 点标记样式
                    marker: new window.TMap.MarkerStyle({
                        width: 20, // 样式宽
                        height: 30, // 样式高
                        anchor: { x: 10, y: 30 }, // 描点位置
                    }),
                },
                geometries: [
                    // 点标记数据数组
                    {
                        // 标记位置(纬度，经度，高度)
                        position: this.map.getCenter(),
                        id: 'marker',
                    },
                ],
            });
        },
        // 保存
        save() {
            if (this.editedIndex > -1) {
                // 更新
                this.$axios.post('/xiaoqu/update', this.editedItem)
                    .then((res) => {
                        console.log(res)
                        this.close()
                        this.getlist()
                        this.snackbar = {
                            show: true,
                            text: res.data.message,
                            color: res.data.errors ? "error" : "success",
                        }
                    })
                    .catch(error => {
                        console.log(error)
                        this.snackbar = {
                            show: true,
                            text: error,
                            color: "error"
                        }
                    })
            } else {
                // 添加
                this.$axios.post('/xiaoqu/create', this.editedItem)
                    .then((res) => {
                        console.log(res)
                        this.close()
                        this.getlist()
                        this.snackbar = {
                            show: true,
                            text: res.data.message,
                            color: res.data.errors ? "error" : "success",
                        }
                    })
                    .catch(error => {
                        console.log(error)
                        this.snackbar = {
                            show: true,
                            text: error,
                            color: "error"
                        }
                    })
            }
        },
    },
}
</script>